<template>
  <div class="commonProblem">
    <el-card class="common-table-container">
      <div class="common-header-box">
        <h3>常见问题列表</h3>
        <el-button type="primary" @click="addBtn">添加</el-button>
      </div>
      <el-table ref="multipleTable" :data="tableData" border height="100%">
        <el-table-column type="selection" width="55"></el-table-column>
        <el-table-column v-for="item in tableColumn" :key="item.label" :label="item.label" :prop="item.prop"
          :min-width="item.width || 160"></el-table-column>
        <el-table-column label="操作" width="200px">
          <template slot-scope="scope">
            <el-button type="text" @click="editBtn(scope.row)">修改</el-button>
            <el-button type="text" @click="delBtn(scope.row)">删除</el-button>
          </template>
        </el-table-column>
      </el-table>
      <div class="common-table-footer">
        <div></div>
        <el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange"
          :current-page="params.pageNum" :page-sizes="[10, 20, 30, 40]" :page-size="params.pageSize"
          layout="total, sizes, prev, pager, next, jumper" :total="total">
        </el-pagination>
      </div>
    </el-card>
    <el-dialog :title="isEdit ? '修改指南' : '添加指南'" :visible.sync="dialogVisible" width="20%">
      <el-form :model="addForm" ref="addForm" :rules="rules">
        <el-form-item label="标题：" prop="title">
          <el-input v-model="addForm.title" placeholder="请填写标题"></el-input>
        </el-form-item>
        <el-form-item label="描述：" prop="content">
          <el-input type="textarea" v-model="addForm.content" placeholder="请填写描述" :rows="7"></el-input>
        </el-form-item>
        <el-form-item label="排序：" prop="sort">
          <!-- <el-input v-model="addForm.sort" type="number" placeholder="请填写数字"></el-input> -->
          <el-input-number v-model="addForm.sort" controls-position="right" :min="0"></el-input-number>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button style="width: 100%;" type="primary" @click="submitForm(isEdit)">确 定</el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
export default {
  name: "CommonProblem",
  data() {
    return {
      rules: {
        //表单验证规则
        title: [
          { required: true, message: "请填写标题", trigger: "blur" },
        ],
        content: [
          { required: true, message: "请填写描述", trigger: "blur" },
        ],
        sort: [
          { required: true, message: "请填写数字", trigger: "blur" },
        ]
      },
      params: {
        pageNum: 1,
        pageSize: 10,
      },
      addForm: {
        title: '',
        content: '',
        sort: '',
      },
      tableData: [],
      total: 0, //总条数
      tableColumn: [
        { prop: "id", label: "编号", width: 120 },
        { prop: "title", label: "标题" },
        { prop: "content", label: "描述" },
        { prop: "sort", label: "排序", width: 55 },
      ],
      dialogVisible: false,
      isEdit: false
    }
  },
  created() {
    this.getProblemList()
  },
  methods: {
    // 获取列表
    getProblemList() {
      this.$Axios.post('/faq/page', JSON.stringify(this.params), { contentType: 'application/json' })
        .then(res => {
          if (res.data.data) {
            this.tableData = res.data.data.records;
            this.total = res.data.data.total;
          }
        }).catch(err => console.log(err))
    },
    handleCurrentChange(page) {
      this.params.pageNum = page;
      this.getProblemList();
    },
    handleSizeChange(val) {
      this.params.pageSize = val;
      this.getProblemList();
    },
    // 添加
    addBtn() {
      this.isEdit = false;
      this.addForm = {};
      this.dialogVisible = true;
    },
    // 修改
    editBtn(row) {
      this.isEdit = true;
      this.addForm = { ...row };
      this.dialogVisible = true;
    },
    // 删除
    delBtn(row) {
      this.$confirm("确定要删除吗?", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning",
      })
        .then(() => {
          this.$Axios.delete('/faq/delete/' + row.id)
            .then(res => {
              if (res.data.data) {
                this.$message.success('删除成功');
                this.getProblemList();
              }
            }).catch(err => console.log(err))
        })
        .catch(() => {
          console.log('已取消');
        });
    },
    submitForm(isEdit) {
      this.$refs.addForm.validate(async (vali) => {
        if (vali) {
          if (isEdit) {
            this.$Axios.put('/faq', JSON.stringify(this.addForm), { contentType: 'application/json' })
              .then(res => {
                if (res.data.data) {
                  this.$message.success('修改成功');
                  this.getProblemList();
                  this.dialogVisible = false;
                }
              }).catch(err => console.log(err))
          } else {
            this.$Axios.post('/faq/add', JSON.stringify(this.addForm), { contentType: 'application/json' })
              .then(res => {
                if (res.data.data) {
                  this.$message.success('添加成功');
                  this.getProblemList();
                  this.dialogVisible = false;
                }
              }).catch(err => console.log(err))
          }
        } else {
          return;
        }
      })

    }
  },
};
</script>

<style lang="less" scoped>
