<template>
  <div class="systemLog">
    <el-card class="common-search-box">
      <div class="common-search-container">
        <div class="search-item">
          <SearchDatePicker
            title="操作时间"
            :value="params.dateRange"
            @searchDateChange="searchDateChange"
          ></SearchDatePicker>
        </div>
        <div class="search-item">
          <span class="title">操作用户</span>
          <el-select v-model="params.userId" placeholder="全部用户" clearable>
            <el-option
              v-for="item in userTypeList"
              :key="item.id"
              :label="item.nickName"
              :value="item.id"
              filterable
              clearable
            >
            </el-option>
          </el-select>
        </div>
        <div class="search-item search-btn">
          <el-button @click="query('query')" type="primary">查询</el-button>
          <el-button @click="reset">重置</el-button>
        </div>
      </div>
    </el-card>
    <el-card class="common-table-container">
      <div class="common-header-box">
        <h3 class="logTitle">日志列表</h3>
      </div>
      <ul class="logList">
        <li v-for="item in logData" :key="`${item.userId}-${item.createTime
}`">
            <p>{{item.title}}</p>
            <p>{{item.createTime}}</p>
        </li>
      </ul>
      <div class="common-table-footer">
        <div></div>
        <el-pagination
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
          :current-page="params.pageNum"
          :page-sizes="[10, 20, 30, 40]"
          :page-size="params.pageSize"
          layout="total, sizes, prev, pager, next, jumper"
          :total="total"
        >
        </el-pagination>
      </div>
    </el-card>
  </div>
</template>

<script>
import utils from "@/api/utils";
export default {
  name: "SystemLog",
  data() {
    return {
      params: {
        dateRange: "",
        userId: "",
        pageNum: 1,
        pageSize: 10,
      },
      params2: {
        dateRange: "",
        userId: "",
        pageNum: 1,
        pageSize: 10,
      },
      logData: [],
      userTypeList: [],
      total: 0, //总条数
    }
  },
  created() {
    this.queryuserType();
    this.query()
  },
  methods: {
    searchDateChange(val) {
      this.params.dateRange = val;
    },
    // 获取用户列表数据
    queryuserType() {
      let params = {
        pageSize: 100,
        pageNum: 1,
        isAdmin: true
      }
      this.$Axios.post('/user/page', JSON.stringify(params),{contentType: 'application/json'})
      .then( res=> {
        if( res.data.data ) {
          this.userTypeList = res.data.data.records
        }
      }).catch( err=>console.log(err))
    },
    // 查询
    query(type) {
      if (type == "query") {
        this.params.pageNum = 1;
        this.params2 = JSON.parse(JSON.stringify(this.params));
        // console.log("query-params2",this.params2);
      } else {
        this.params2.pageNum = this.params.pageNum;
        this.params2.pageSize = this.params.pageSize;
      }
      let params = JSON.parse(
        JSON.stringify(utils.objDeleteNull(this.params2))
      );
      // console.log("params",this.params);
      params.dateRange = this.params2.dateRange
        ? this.params2.dateRange.join(" ~ ")
        : null;
      this.$Axios
        .post("/log-operation/page", JSON.stringify(params),{contentType: 'application/json'})
        .then((res) => {
          if( res.data.data ) {
            this.logData = res.data.data.records;
            this.total = res.data.data.total;
          }
        })
        .catch((err) => {
          console.log(err);
        });
    },
    // 重置
    reset() {
      this.params.userId = null;
      this.params.dateRange = null;
      this.query("query");
    },
    // 当前页码改变
    handleCurrentChange(page) {
      this.params.pageNum = page;
      this.query();
    },
    // 每页显示条数改变
    handleSizeChange(val) {
      this.params.pageSize = val;
      this.query();
    },
  },
};
</script>

<style lang="less" scoped>
.systemLog {
  // height: 100%;
  .logTitle {
    border-bottom: 1px solid #D7D7D7;
    padding-bottom: 20px;
    margin-bottom: 0;
    width: 100%;
  }
  .logList {
    li {
        display: flex;
        justify-content: space-between;
        padding: 25px 10px;
        font-size: 14px;
        border-bottom: 1px solid #F2F2F2;
    }
  }
}
</style>