<template>
  <div class="auto-register">
    <el-card class="common-search-box">
      <h2 class="title">自动注册用户列表</h2>
      <div class="common-search-container">
        <div class="search-item">
          <el-input class="item" v-model="params.unionId" @input="params.unionId = params.unionId.trim()" clearable
            placeholder="用户ID"></el-input>
          <el-input class="item" v-model="params.mobile" @input="params.mobile = params.mobile.trim()" clearable
            placeholder="手机号"></el-input>
          <el-input class="item" v-model="params.note" @input="params.note = params.note.trim()" clearable
            placeholder="备注模糊查询"></el-input>
        </div>
        <div class="search-item search-btn">
          <el-button @click="getData" type="primary">查询</el-button>
          <el-button @click="batchRegister" type="success">批量注册</el-button>
          <el-button @click="reset">重置</el-button>
        </div>
      </div>
    </el-card>
    <el-card class="common-table-container">
      <div class="common-header-box">
        <h3>注册人数：{{ total }}人</h3>
      </div>
      <el-table ref="multipleTable" class="auto-register-table" :data="tableData" border style="width: 100%"
        height="100%">
        <el-table-column prop="unionId" label="ID">
        </el-table-column>
        <el-table-column prop="nickName" label="昵称">
        </el-table-column>
        <el-table-column prop="mobile" label="手机号">
        </el-table-column>
        <el-table-column prop="regTime" label="注册时间">
        </el-table-column>
        <el-table-column prop="note" label="备注" fixed="right" min-width="200" align="left">
          <template slot-scope="scope">
            <el-popconfirm class="note-pop" confirm-button-text='确认' cancel-button-text='取消' icon="el-icon-info"
              icon-color="red" @confirm="setCommonUser(scope.row.id)" title="确认操作？">
              <el-button slot="reference" type="primary" size="mini">设为普通
              </el-button>
            </el-popconfirm>
            <el-button class="note-btn" type="primary" size="mini" icon="el-icon-edit"
              @click="eidtNoteHandle(scope.row)">编辑
            </el-button>
            <div class="note">{{ scope.row.note }}</div>
          </template>
        </el-table-column>
        <!-- <el-table-column v-for="item in tableColumn" :key="item.label" :label="item.label" :prop="item.prop"
          :min-width="item.width || 120" :fixed="item.label == '备注' ? 'right' : ''">
          <template slot-scope="scope">
            <div v-if="item.prop === 'note'">
              <el-popconfirm class="note-pop" confirm-button-text='确认' cancel-button-text='取消' icon="el-icon-info"
                icon-color="red" @confirm="setCommonUser(scope.row.id)" title="确认操作？">
                <el-button slot="reference" type="primary" size="mini">设为普通
                </el-button>
              </el-popconfirm>
              <el-button class="note-btn" type="primary" size="mini" icon="el-icon-edit"
                @click="eidtNoteHandle(scope.row)">编辑
              </el-button>
              <div class="note">{{ scope.row.note }}</div>
            </div>
            <div v-else>{{ scope.row[item.prop] }}</div>
          </template>
        </el-table-column> -->
      </el-table>

      <div class="common-table-footer">
        <el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange"
          :current-page="params.pageNum" :page-sizes="[10, 20, 50, 100]" :page-size="params.pageSize"
          layout="total, sizes, prev, pager, next, jumper" :total="total">
        </el-pagination>
      </div>
    </el-card>
    <!-- 批量注册弹出层 -->
    <el-dialog v-if="registerDialogVisible" title="批量注册用户" :visible.sync="registerDialogVisible" width="25%">
      <el-form ref="registerForm" :model="registerForm" label-width="140px" class="edit-pwd">
        <el-form-item label="注册用户手机号：">
          <el-input type="textarea" v-model="registerForm.mobile" rows="10"></el-input>
          <div class="tips"> * 多个手机号之间以英文逗号分割，上限10个</div>
        </el-form-item>
        <el-form-item label="设置注册密码：">
          <el-input show-password v-model="registerForm.password"></el-input>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="submitRegister">确 定</el-button>
        </el-form-item>
      </el-form>
    </el-dialog>
    <!-- 编辑备注弹出层 -->
    <el-dialog v-if="noteDialogVisible" title="编辑用户备注" :visible.sync="noteDialogVisible" width="25%">
      <el-form ref="editNoteForm" :model="editNoteForm" label-width="120px" class="edit-pwd">
        <el-form-item label="备注：">
          <el-input type="textarea" v-model="editNoteForm.note" maxlength="30" show-word-limit></el-input>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="submitNote">确 定</el-button>
        </el-form-item>
      </el-form>
    </el-dialog>
  </div>
</template>

<script>
// import utils from "@/api/utils";
export default {
  name: "AutoRegisterUser",
  data() {
    return {
      params: {
        unionId: "",
        mobile: "",
        note: "",
        pageSize: 10,
        pageNum: 1,
      },
      registerDialogVisible: false, //控制批量注册弹窗显示
      noteDialogVisible: false, //控制编辑备注弹窗显示
      tableData: [], //注册用户列表数据
      total: 0, //总条数
      editNoteForm: {
        note: ""
      },
      //批量注册表单
      registerForm: {
        mobile: "",
        password: ""
      },
    };
  },
  created() {
    this.pageSysUser(); //初始化系统用户列表
  },
  methods: {
    // 条件分页查询系统用户列表
    async pageSysUser() {
      let res = await this.$Axios.post("user/sysUser/page", JSON.stringify({
        unionId: this.params.unionId,
        mobile: this.params.mobile,
        note: this.params.note,
        pageSize: this.params.pageSize,
        pageNum: this.params.pageNum
      }), { contentType: 'application/json' })
      let { code, data } = res.data;
      if (code === "000000") {
        this.total = data.total;
        this.tableData = data.records;
      }
    },
    // 设置为普通用户
    async setCommonUser(id) {
      if (id) {
        let res = await this.$Axios.put("user/change/SysUser", { id });
        let { code } = res.data;
        if (code === "000000") {
          this.$message.success("操作成功！");
          this.pageSysUser(); //重绘视图
        }
      }
      // console.log(res);
    },
    // 根据条件进行查询
    getData() {
      if ((this.total / this.params.pageSize) < this.params.pageNum) { //判断所请求的数据页码与当前页码大小
        this.params.pageNum = 1;
      }
      this.pageSysUser(); //根据筛选条件获得数据
    },
    // 重置按钮
    reset() {
      this.params.unionId = "";
      this.params.mobile = "";
      this.params.note = "";
      this.pageSysUser(); //重绘视图
    },
    // 批量注册
    batchRegister() {
      this.registerDialogVisible = true;
    },
    // 批量注册弹窗---确认修改
    async submitRegister() {
      let res = await this.$Axios.post("user/register_SysUser", JSON.stringify(this.registerForm), { contentType: 'application/json' });
      let { code } = res.data;
      if (code === "000000") {
        this.$message.success("操作成功！");
        this.registerDialogVisible = false;
        this.registerForm.mobile = "";
        this.registerForm.password = "";
        this.pageSysUser(); //重绘注册视图
      }
    },
    //编辑单行备注内容---确认修改
    eidtNoteHandle(row) {
      this.editNoteForm = { ...row };
      this.noteDialogVisible = true;
    },
    // 保存备注修改内容
    async submitNote() {
      let res = await this.$Axios.post("user/note/SysUser", JSON.stringify({
        id: this.editNoteForm.id,
        note: this.editNoteForm.note
      }), { contentType: 'application/json' });
      let { code } = res.data;
      if (code === "000000") {
        this.$message.success("编辑成功！");
        this.noteDialogVisible = false;
        this.pageSysUser(); //重绘视图
      }
    },
    // 当前页改变
    handleCurrentChange(val) {
      this.params.pageNum = val;
      this.pageSysUser(); //重绘视图
    },
    // 每页显示条数改变
    handleSizeChange(val) {
      this.params.pageSize = val;
      this.pageSysUser(); //重绘视图
    }
  },
  computed: {},
};
</script>

<style lang="less" scoped>
.auto-register {


  // height: 100%;
  .title {
    padding-bottom: 10px;
    margin-bottom: 20px;
    border-bottom: 1px solid #ccc;
    color: rgb(129, 129, 129);
    font-size: 16px;
    font-weight: normal;
  }

  h3 {
    padding: 0 5px;
    width: 100%;
    font-size: 14px;
    font-weight: bold;
    color: #444;
    background-color: #f2f2f2;
  }

  .note {
    display: inline-block;
    margin: 0 10px;
    width: 400px;
    text-align: left;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
  }

  .date-picker {
    display: inline-block;
    margin-right: 10px;
  }

  .tips {
    font-size: 12px;
    color: rgb(216, 15, 15);
  }

  .common-search-container {
    .item {
      width: 140px;
      margin-right: 10px;
    }

    .el-select {
      width: 180px;
      margin-right: 15px;

      /deep/ .el-input {
        width: 100%;
        margin-bottom: 10px;
      }
    }
  }

  /deep/ .el-popover__reference-wrapper {
    margin-right: 10px;
  }
}
</style>