<template>
  <div class="user-list">
    <el-card class="common-search-box">
      <h2 class="title">用户列表</h2>
      <div class="common-search-container">
        <div class="search-item">
          <el-input class="item" v-model="params.id" @input="params.id = params.id.trim()" clearable placeholder="编号">
          </el-input>
          <el-input class="item" v-model="params.unionId" clearable @input="params.unionId = params.unionId.trim()"
            placeholder="用户ID">
          </el-input>
          <el-input class="item" v-model="params.mobile" clearable @input="params.mobile = params.mobile.trim()"
            placeholder="手机号"></el-input>
          <el-select v-model="params.onlineStatus" placeholder="请选择状态">
            <el-option label="在线" :value="0"></el-option>
            <el-option label="离线" :value="1"></el-option>
          </el-select>
          <el-select v-model="params.isSysUser" placeholder="全部用户">
            <el-option label="系统用户" :value="true"></el-option>
            <el-option label="普通用户" :value="false"></el-option>
          </el-select>
        </div>
        <div class="search-item">
          <SearchDatePicker title="注册日期" :value="params.dateRange" @searchDateChange="searchDateChange($event)">
          </SearchDatePicker>
        </div>
        <div class="search-item search-btn">
          <el-button @click="getData" type="primary">查询</el-button>
          <el-button @click="reset">重置</el-button>
        </div>
      </div>
    </el-card>
    <el-card class="common-table-container">
      <el-table ref="multipleTable" :data="tableData" border style="width: 100%" height="100%">
        <el-table-column v-for="item in tableColumn" :key="item.label" :label="item.label" :prop="item.prop"
          :min-width="item.width || 120">
          <!-- <template slot-scope="scope">
            <el-switch v-if="item.prop == 'status'" v-model="scope.row.status" @change="isOpenUser(scope.row)">
            </el-switch>
            <div v-else-if="item.prop == 'headUrl'">
              <img :src="scope.row.headUrl" v-if="scope.row.headUrl" v-imgview />
              <span v-else>暂无图片</span>
            </div>
            <div v-else>{{ scope.row[item.prop] }}</div>
          </template> -->
        </el-table-column>
        <el-table-column label="操作" width="400px">
          <template slot-scope="scope">
            <el-popconfirm class="popconfirm" confirm-button-text='确认' cancel-button-text='取消' icon="el-icon-info"
              icon-color="red" @confirm="offlineHandle(scope.row.id)" title="确认踢下线？">
              <el-button slot="reference" size="mini" type="danger">下线</el-button>
            </el-popconfirm>
            <el-popconfirm class="popconfirm" confirm-button-text='确认' cancel-button-text='取消' icon="el-icon-info"
              icon-color="red" @confirm="banHandle(scope.row.id)" title="确认封号？">
              <el-button slot="reference" size="mini" type="danger">封号</el-button>
            </el-popconfirm>
            <el-popconfirm class="popconfirm" confirm-button-text='确认' cancel-button-text='取消' icon="el-icon-info"
              icon-color="red" @confirm="setSystemHandle(scope.row.id)" title="确认操作？">
              <el-button size="mini" type="primary" slot="reference">设系统</el-button>
            </el-popconfirm>
            <el-button class="edit-pwd-btn" size="mini" type="warning" @click="editPwdHandle(scope.row)">改密</el-button>
            <el-popconfirm class="popconfirm" confirm-button-text='确认' cancel-button-text='取消' icon="el-icon-info"
              icon-color="red" @confirm="thawHandle(scope.row.id)" title="确认解冻？">
              <el-button size="mini" type="success" slot="reference">解冻</el-button>
            </el-popconfirm>
          </template>
        </el-table-column>
      </el-table>

      <div class="common-table-footer">
        <el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange"
          :current-page="params.pageNum" :page-sizes="[10, 50, 100, 200, 500]" :page-size="params.pageSize"
          layout="total, sizes, prev, pager, next, jumper" :total="total">
        </el-pagination>
      </div>
    </el-card>
    <!-- 弹出层 -->
    <el-dialog v-if="dialogVisible" title="密码设置" :visible.sync="dialogVisible" width="25%" @close="closeEditPwd">
      <el-form ref="editPwdForm" :rules="rules" :model="editPwdForm" label-width="120px" class="edit-pwd">
        <el-form-item label="设置新密码" prop="password">
          <el-input show-password v-model="editPwdForm.password"></el-input>
        </el-form-item>
        <el-form-item label="重新输入新密码" prop="confirmPassword">
          <el-input show-password v-model="editPwdForm.confirmPassword"></el-input>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="submitPwd">确 定</el-button>
        </el-form-item>
      </el-form>
    </el-dialog>
  </div>
</template>

<script>
// import utils from "@/api/utils";
export default {
  name: "InquiryUserList",
  data() {
    var validatePass = (rule, value, callback) => {
      if (value === '') {
        callback(new Error('请输入密码'));
      } else {
        if (this.editPwdForm.confirmPassword !== '') {
          this.$refs.editPwdForm.validateField('confirmPassword');
        }
        callback();
      }
    };
    var validatePass2 = (rule, value, callback) => {
      if (value === '') {
        callback(new Error('请再次输入密码'));
      } else if (value !== this.editPwdForm.password) {
        callback(new Error('两次输入密码不一致!'));
      } else {
        callback();
      }
    };
    return {
      params: {
        unionId: null,
        id: null,
        mobile: null,
        onlineStatus: null,
        isSysUser: null,
        dateRange: null,
        pageSize: 10,
        pageNum: 1,
      },
      dialogVisible: false, //控制弹窗显示
      tableData: [], //用户列表数据
      //改密表单
      editPwdForm: {
        password: "",
        confirmPassword: ""
      },
      successEditPwdForm: {
        id: null,
        password: null,
      },
      total: 0, //总条数
      //用户列表表头
      tableColumn: [
        { prop: "id", label: "编号", width: 150 },
        { prop: "unionId", label: "用户ID" },
        { prop: "nickName", label: "昵称" },
        { prop: "mobile", label: "手机号" },
        { prop: "balance", label: "账户余额" },
        { prop: "regTime", label: "注册时间", width: 200 },
        { prop: "loginTime", label: "最后登录时间", width: 200 }
      ],
      rules: {
        password: [
          { validator: validatePass, trigger: 'blur' },
        ],
        confirmPassword: [
          { validator: validatePass2, trigger: 'blur' }
        ]
      }
    };
  },
  created() {
    this.pageUserList(); //初始化用户列表数据
  },
  methods: {
    // 根据搜索条件筛选
    getData() {
      if ((this.total / this.params.pageSize) < this.params.pageNum) { //判断所请求的数据页码与当前页码大小
        this.params.pageNum = 1;
      }
      this.pageUserList();
    },
    // 下线
    async offlineHandle(id) {
      // console.log("踢下线", row);
      let res = await this.$Axios.post(`user/offline/${id}`);
      let { code } = res.data;
      console.log("踢下线", res);
      if (code === "000000") {
        this.$message.success("操作成功！");
      }
    },
    // 封号
    async banHandle(id) {
      let res = await this.$Axios.post(`user/close/${id}`);
      let { code } = res.data;
      console.log("封号", res);
      if (code === "000000") {
        this.$message.success("操作成功！");
      }
    },
    // 设系统
    async setSystemHandle(id) {
      let res = await this.$Axios.post(`user/set-sys/${id}`);
      let { code } = res.data;
      console.log("设系统", res);
      if (code === "000000") {
        this.$message.success("操作成功！");
      }
    },
    // 改密
    editPwdHandle(row) {
      this.dialogVisible = true;
      this.successEditPwdForm.id = row.id;
    },
    // 密码修改弹窗---确认修改
    submitPwd() {
      // let res = await this.$Axios.
      this.successEditPwdForm.password = this.editPwdForm.confirmPassword;
      this.$refs.editPwdForm.validate(async (valid) => {
        if (valid) {
          let res = await this.$Axios.post("user/update-password", this.successEditPwdForm)
          let { code } = res.data;
          if (code === "000000") {
            this.$message.success("密码修改成功！")
            this.dialogVisible = false;
            this.editPwdForm = {
              password: "",
              confirmPassword: ""
            }
          }
        }
      });
    },
    // 关闭改密弹窗
    closeEditPwd() {
      this.editPwdForm = {
        password: "",
        confirmPassword: ""
      }
    },
    // 解冻
    async thawHandle(id) {
      let res = await this.$Axios.post(`user/open/${id}`);
      let { code } = res.data;
      console.log("解冻", res);
      if (code === "000000") {
        this.$message.success("操作成功！");
      }
    },
    // 条件分页获取用户列表
    async pageUserList() {
      let res = await this.$Axios.post("user/page", JSON.stringify({
        pageSize: this.params.pageSize,
        pageNum: this.params.pageNum,
        id: this.params.id ? this.params.id : null,
        unionId: this.params.unionId ? this.params.unionId : null,
        mobile: this.params.mobile ? this.params.mobile : null,
        onlineStatus: this.params.onlineStatus || this.params.onlineStatus === 0 ? this.params.onlineStatus : null,
        isSysUser: this.params.isSysUser || this.params.isSysUser === false ? this.params.isSysUser : null,
        dateRange: this.params.dateRange ? `${this.params.dateRange[0]}~${this.params.dateRange[1]}` : null
      }), { contentType: 'application/json' });
      let { code, data } = res.data;
      if (code === "000000") {
        this.total = data.total;
        this.tableData = data.records;
      }
    },
    // 获取群报表搜索的回传日期
    searchDateChange(val) {
      this.params.dateRange = val;
    },
    // 重置按钮
    reset() {
      this.params.unionId = null;
      this.params.id = null;
      this.params.mobile = null;
      this.params.onlineStatus = null;
      this.params.isSysUser = null;
      this.params.dateRange = null;
      this.pageUserList(); //重绘视图
    },
    // 当前页改变
    handleCurrentChange(val) {
      this.params.pageNum = val;
      this.pageUserList(); //重绘视图
    },
    // 每页显示条数改变
    handleSizeChange(val) {
      this.params.pageSize = val;
      this.pageUserList(); //重绘视图
    }
  },
  computed: {},
};
</script>

