<template>
  <div class="HomeMain">
    <el-card class="common-search-box">
      <div class="common-search-container">
        <div class="search-item">
          <span class="title">统计日期</span>
          <el-date-picker
            v-model="date"
            type="daterange"
            range-separator="至"
            start-placeholder="开始日期"
            end-placeholder="结束日期"
            value-format="yyyy-MM-dd"
            :picker-options="pickerOptions"
            @change="searchStatistics"
          >
          </el-date-picker>
        </div>
      </div>
    </el-card>
    <el-row :gutter="20">
      <el-col :span="8" v-for="item in statisticsList" :key="item.id">
        <el-card>
          <h3>{{ item.itemTypeName }}</h3>
          <div class="dataInfo-box">
            <el-avatar :size="80" :src="item.imgUrl"></el-avatar>
            <div class="count-box">
              <h5>{{ item.dayLabel }}</h5>
              <span>{{ item.dayNumValue }}</span>
            </div>
          </div>
          <p class="total-box">{{ item.cumulativeLabel }} <span>{{ item.cumulativeNumValue }}</span></p>
        </el-card>
      </el-col>
    </el-row>
  </div>
</template>

<script>
export default {
  name: "HomeMain",
  data() {
    return {
      date: [],
      statisticsList: [],
      imgList: [
        {
          itemType: 15001,
          imgUrl: require("@/assets/img/user.png"),
        },
        {
          itemType: 15002,
          imgUrl: require("@/assets/img/recharge.png"),
        },
        {
          itemType: 15003,
          imgUrl: require("@/assets/img/cash-out.png"),
        },
        {
          itemType: 15004,
          imgUrl: require("@/assets/img/red-packet.png"),
        },
        {
          itemType: 15005,
          imgUrl: require("@/assets/img/red-packet.png"),
        },
        {
          itemType: 15006,
          imgUrl: require("@/assets/img/red-packet.png"),
        },
        {
          itemType: 15007,
          imgUrl: require("@/assets/img/red-packet.png"),
        },
        {
          itemType: 15008,
          imgUrl: require("@/assets/img/red-packet.png"),
        },
        {
          itemType: 15009,
          imgUrl: require("@/assets/img/red-packet.png"),
        },
        {
          itemType: 15010,
          imgUrl: require("@/assets/img/red-packet.png"),
        },
        {
          itemType: 15011,
          imgUrl: require("@/assets/img/red-packet.png"),
        },
        {
          itemType: 15012,
          imgUrl: require("@/assets/img/transfer-cash.png")
        }
      ],
    };
  },
  created() {
    this.getStatisticsData();
  },
  methods: {
    searchStatistics(val) {
      this.getStatisticsData(val)
    },
    getStatisticsData(date) {
      let params = {
        dateRange: date ? date.join(" ~ ") : null
      }
      this.$Axios.post('/statistics/list',JSON.stringify(params),{contentType: 'application/json'})
      .then( res=> {
        if( res.data.data ) {
          res.data.data.forEach(item => {
            this.imgList.forEach(item2 => {
              if(item2.itemType == item.itemType) {
                item.imgUrl = item2.imgUrl;
              }
            });
          });
          this.statisticsList = res.data.data;
        }
      }).catch( err=>console.log(err))
    }
  },
  computed: {
    pickerOptions() {
      return {
        disabledDate(time) {
          return time.getTime() > Date.now();
        },
      };
    },
  }
};
</script>

<style lang="less" scoped>
.el-col {
  margin-bottom: 20px;
  &:last-child {
    margin-bottom: 40px;
  }
  /deep/ .el-card {
    font-size: 14px;
    color: #999999;
    .el-card__body {
      padding: 20px 30px;
      min-height: 180px;
      display: flex;
      flex-direction: column;
      justify-content: space-around;
      // h3 {
      // }
    }
  }
  .dataInfo-box {
    display: flex;
    align-items: center;
    .count-box {
      margin-left: 20px;
      h5 {
        margin-bottom: 20px;
        font-size: 12px;
        font-weight: normal;
      }
      span {
        font-size: 24px;
        font-weight: 700;
        color: #000000;
      }
    }
  }
  .total-box {
    span {
      color: #555555;
      font-weight: 650;
      margin-left: 5px;
    }
  }
}
</style>
