<template>
  <div class="personal-red-packet">
    <el-card class="common-search-box">
      <h2 class="title">个人红包列表</h2>
      <div class="common-search-container">
        <div class="search-item">
          <el-input class="item" v-model="params.fromUnionId" @input="params.fromUnionId = params.fromUnionId.trim()"
            clearable placeholder="发送者ID"></el-input>
          <el-input class="item" v-model="params.toUnionId" @input="params.toUnionId = params.toUnionId.trim()" clearable
            placeholder="接收者ID"></el-input>
          <el-input class="item" v-model="params.moneyAmount" @input="params.moneyAmount = params.moneyAmount.trim()"
            clearable placeholder="红包金额"></el-input>
          <el-select v-model="params.status" placeholder="领取状态">
            <el-option label="已发送未领取" :value="1"></el-option>
            <el-option label="已发送已领取" :value="3"></el-option>
            <el-option label="已发送未领取全部退款" :value="4"></el-option>
          </el-select>
          <div class="search-item date-picker">
            <el-date-picker v-model="params.createTime" value-format="yyyy-MM-dd" type="date" placeholder="选择红包日期">
            </el-date-picker>
          </div>
          <div class="search-item date-picker">
            <el-date-picker v-model="params.receiveOrRefundTime" value-format="yyyy-MM-dd" type="date"
              placeholder="选择领/退日期">
            </el-date-picker>
          </div>
        </div>
        <div class="search-item search-btn">
          <el-button @click="getData" type="primary">查询</el-button>
          <el-button @click="reset">重置</el-button>
        </div>
      </div>
    </el-card>
    <el-card class="common-table-container">
      <div class="common-header-box">
        <h3>总金额：{{ totalAmount }}元</h3>
      </div>
      <el-table ref="multipleTable" :data="tableData" border style="width: 100%" height="100%">
        <el-table-column v-for="item in tableColumn" :key="item.label" :label="item.label" :prop="item.prop"
          :min-width="item.width || 120">
          <!-- <template slot-scope="scope">
            <div v-if="item.prop == 'status'">{{ numToMap[scope.row.status] }}</div>
            <div v-else>{{ scope.row[item.prop] }}</div>
          </template> -->
          <template slot-scope="scope">
            <div v-if="item.prop === 'status'">
              {{ redPacketStatus[scope.row.status] }}
            </div>
            <div v-else>{{ scope.row[item.prop] }}</div>
          </template>
        </el-table-column>
        <el-table-column label="操作" width="200px">
          <template slot-scope="scope">
            <el-button slot="reference" size="small" :type="scope.row.status == '1' ? 'success' : 'info'"
              :disabled="scope.row.status == '1' ? false : true" @click="receiveRedPacket(scope.row)">{{ scope.row.status ==
                '1' ? "领取" : "已领取" }}</el-button>
          </template>
        </el-table-column>
      </el-table>

      <div class="common-table-footer">
        <el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange"
          :current-page="params.pageNum" :page-sizes="[10, 20, 50, 100]" :page-size="params.pageSize"
          layout="total, sizes, prev, pager, next, jumper" :total="total">
        </el-pagination>
      </div>
    </el-card>
  </div>
</template>

<script>
// import utils from "@/api/utils";
export default {
  name: "PersonalRedPacketList",
  data() {
    return {
      params: {
        fromUnionId: "",
        toUnionId: "",
        moneyAmount: "",
        status: null,
        createTime: "",
        receiveOrRefundTime: "",
        pageSize: 10,
        pageNum: 1,
      },
      totalAmount: 0, //总金额
      tableData: [], //个人红包列表数据
      total: 0, //总条数
      // 红包状态判断
      redPacketStatus: {
        "1": "已发送未领取",
        "3": "已发送已领取",
        "4": "已发送未领取全部退款",
      },
      //个人红包列表表头
      tableColumn: [
        { prop: "id", label: "红包ID" },
        { prop: "fromUnionId", label: "发送ID" },
        { prop: "fromUserNickName", label: "发送昵称" },
        { prop: "toUnionId", label: "接收ID" },
        { prop: "toUserNickName", label: "接收昵称" },
        { prop: "moneyAmount", label: "金额" },
        { prop: "status", label: "类型" },
        { prop: "createTime", label: "红包时间", width: 200 },
        { prop: "receiveOrRefundTime", label: "领/退时间", width: 200 }
      ]
    };
  },
  created() {
    this.pagePersonalRedPacket(); //初始化个人红包列表
  },
  methods: {
    // 分页查询个人红包列表
    async pagePersonalRedPacket() {
      // console.log("NaN", isNaN(this.params.moneyAmount));
      if (isNaN(this.params.moneyAmount)) {
        this.$message.warning("请填写正确的金额！");
        return
      }
      let res = await this.$Axios.post("personal-red-envelope/page", JSON.stringify({
        pageSize: this.params.pageSize,
        pageNum: this.params.pageNum,
        fromUnionId: this.params.fromUnionId,
        toUnionId: this.params.toUnionId,
        moneyAmount: this.params.moneyAmount,
        status: this.params.status ? this.params.status : null,
        createTime: this.params.createTime,
        receiveOrRefundTime: this.params.receiveOrRefundTime
      }), { contentType: 'application/json' });
      // console.log(res);
      let { code, data } = res.data;
      if (code === "000000") {
        this.total = data.total;
        this.totalAmount = data.total != 0 ? data.records[0].totalMoneyAmount : 0;
        this.tableData = data.records;
      }
    },
    // 领取红包操作
    async receiveRedPacket(row) {
      let res = await this.$Axios.put(`personal-red-envelope/${row.id}`);
      let { code } = res.data;
      if (code === "000000") {
        this.$message.success("领取成功！")
        setTimeout(() => {
          this.pagePersonalRedPacket(); //重绘视图
        }, 500)
      }
    },
    // 根据条件查询
    getData() {
      if ((this.total/this.params.pageSize)<this.params.pageNum) { //判断所请求的数据页码与当前页码大小
        this.params.pageNum = 1;
      }
      this.pagePersonalRedPacket();
    },
    // 重置按钮
    reset() {
      this.params.fromUnionId = "";
      this.params.toUnionId = "";
      this.params.moneyAmount = "";
      this.params.createTime = "";
      this.params.receiveOrRefundTime = "";
      this.params.status = null;
      this.pagePersonalRedPacket(); //重绘视图
    },
    // 当前页改变
    handleCurrentChange(val) {
      this.params.pageNum = val;
      this.pagePersonalRedPacket(); //重绘视图
    },
    // 每页显示条数改变
    handleSizeChange(val) {
      this.params.pageSize = val;
      this.pagePersonalRedPacket(); //重绘视图
    }
  },
  computed: {},
};
</script>

