  }

  h3 {
    padding: 0 5px;
    width: 100%;
    font-size: 14px;
    font-weight: bold;
    color: #444;
    background-color: #f2f2f2;
  }

  .date-picker {
    display: inline-block;
    margin-right: 10px;
  }

  .common-search-container {
    .item {
      width: 140px;
      margin-right: 10px;
