<template>
  <div class="assignPermission">
    <el-card class="common-table-container">
      <div class="common-header-box editInfo-headerBox">
        <h3>分配权限</h3>
      </div>
      <el-tree :data="treeData" ref="tree" show-checkbox node-key="id" :default-expanded-keys="[]"
        :default-checked-keys="checked" :props="defaultProps">
      </el-tree>
      <div class="common-table-footer">
        <div>
          <el-button @click="submitPermission" type="primary">保存</el-button>
          <el-button @click="$router.replace({ name: 'PermissionManage' })">取消</el-button>
        </div>
      </div>
    </el-card>
  </div>
</template>

<script>
export default {
  name: "AssignPermission",
  data() {
    return {
      // 以选中的值
      checked: [],
      treeData: [],
      defaultProps: {
        children: "children",
        label: "appellation",
      },
    };
  },
  created() {
    this.getPermissionList();
  },
  methods: {
    getPermissionList() {
      this.$Axios.get('/role/menu-list', { id: String(this.$route.query.id) })
        .then(res => {
          if (res.data.data) {
            this.treeData = res.data.data
            this.dataFor(this.treeData);
          }
        }).catch(err => console.log(err))
    },
    // 数组遍历
    dataFor(data) {
      let self = this;
      for (let v of data) {
        if (v.selected) {
          if (v.children && !!v.children.length) {
            self.dataFor(v.children);
          } else {
            self.checked.push(v.id);
          }
        }
      }
    },
    submitPermission() {
      let permissionResourceIds = this.$refs.tree.getCheckedKeys(true);
      let params = {
        id: String(this.$route.query.id),
        type: 1,
        permissionResourceIds
      }
      this.$Axios.post('/role/permission', JSON.stringify(params), { contentType: 'application/json' })
        .then(res => {
          if (res.data.data) {
            this.$message.success('保存成功');
            // this.getPermissionList(); 
            this.$router.go(-1);
          }
        }).catch(err => console.log(err))
    }
  },
};
</script>

<style lang="less" scoped>
.assignPermission {
  // height: 100%;
}
</style>