import Vue from 'vue'
import Vuex from 'vuex'
import $Axios from '@/api/request.js'
Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    accessToken: sessionStorage.accessToken ? sessionStorage.accessToken : null,
    accountInfo: sessionStorage.accountInfo ? JSON.parse(sessionStorage.accountInfo) : null,
    walletChangeList:[],
  },
  mutations: {
    // 登录保存信息
    ACCOUNTTOKEN (state, accessToken) {
      state.accessToken = accessToken
    },
    // 用户信息
    ACCOUNTINFO (state, accountInfo) {
      state.accountInfo = accountInfo
    },
    // 退出登录，清除登录相关的store数据
    loginOut (state) {
      state.accountInfo = null;
      state.accessToken = null;
    },
    // 获取账变类型列表
    M_getWalletChangeList(state,val) {
      state.walletChangeList = val;
    }
  },
  actions: {
    // 获取账变类型列表
    async A_getWalletChangeList({commit}){
      let res = await $Axios.get('wallet-change/type-list');
      let {code,data} = res.data;
      if (code === "000000") {
        commit("M_getWalletChangeList",data)
      }
    }
  },
  modules: {
  }
})
