<template>
  <div class="msg-code">
    <el-card class="common-search-box">
      <h2 class="title">验证码列表</h2>
      <div class="common-search-container">
        <el-table :data="tableData" border style="width: 100%" height="100%">
          <el-table-column v-for="item in tableColumn" :key="item.label" :label="item.label" :prop="item.prop"
            :min-width="item.width || 120">
          </el-table-column>
        </el-table>
        <div class="common-table-footer">
          <el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange"
          :current-page="pageNum" :page-sizes="[10, 20, 50, 100]" :page-size="pageSize"
          layout="total, sizes, prev, pager, next, jumper" :total="total">
        </el-pagination>
        </div>
      </div>
    </el-card>
  </div>
</template>

<script>
export default {
  name: "MessageCode",
  data() {
    return {
      tableData: [], //验证码列表数据
      total: 0, //总条数
      pageNum: 1, //当前显示页码
      pageSize: 10, //每页显示条数
      //验证码列表表头
      tableColumn: [
        { prop: "mobile", label: "手机号" },
        { prop: "authCode", label: "验证码" },
        { prop: "sendTime", label: "发送时间" }
      ]
    };
  },
  created() {
    this.pageSMS(); //初始化用户接收验证码列表
  },
  methods: {
    // 分页展示用户接收验证码列表
    async pageSMS(){
      let res = await this.$Axios.get("sms/page-list",{
        pageSize:this.pageSize,
        pageNum:this.pageNum
      })
      let {code,data} = res.data;
      if (code === "000000") {
        this.total = data.total;
        this.tableData = data.records;
      }
    },
    // 当前页改变
    handleCurrentChange(val) {
      this.pageNum = val;
      this.pageSMS(); //重绘视图
    },
    // 每页显示条数改变
    handleSizeChange(val) {
      this.pageSize = val;
      this.pageSMS(); //重绘视图
    }
  },
  computed: {},
};
</script>

