import Vue from 'vue'
import App from './App.vue'
import router from './router'
import store from './store'
import ElementUI from 'element-ui'
import 'element-ui/lib/theme-chalk/index.css'
import axios from '@/api/request.js'
//引入样式表
import "@/assets/css/reset.css"
import "@/assets/css/common.css"
// 时间格式插件
import Moment from 'moment'

// 公共组件导入
import SearchDatePicker from './components/SearchDatePicker.vue'


Vue.use(ElementUI, {
  size: 'medium'
})
Vue.config.productionTip = false
Vue.prototype.$Moment = Moment
Vue.prototype.$Axios = axios

Vue.component('SearchDatePicker', SearchDatePicker);

console.log(process.env.NODE_ENV);
//Vue.directive 注册或获取全局指令 图片hover预览 v-imgview。
Vue.directive('imgview', {
  // 当绑定元素插入到 DOM 中。
  inserted: function (el) {
    let div = document.querySelector('.outImgView');
    let img = document.querySelector('.outImgView').querySelector('img');

    el.addEventListener('mouseover', () => {
      img.src = el.src;
      div.style.display = 'flex';

      let clientWidth = window.innerWidth;
      let clientHeight = window.innerHeight;
      let elTop = el.getBoundingClientRect().top;
      let elBottom = el.getBoundingClientRect().bottom;
      let elLeft = el.getBoundingClientRect().right;
      let elRight = el.getBoundingClientRect().left;

      // if (clientHeight - elBottom < 600) {
      //   div.style.top = 'auto';
      //   div.style.bottom = clientHeight - elBottom + 'px';
      // } else {
      //   div.style.top = elTop + 'px';
      //   div.style.bottom = 'auto';
      // }
      if (clientWidth - elRight < 600) {
        div.style.left = 'auto';
        div.style.right = clientWidth - elRight + 30 + 'px';
      } else {
        div.style.left = elLeft + 30 + 'px';
        div.style.right = 'auto';
      }
    });
    el.addEventListener('mouseout', () => {
      img.src = '';
      div.style.display = 'none';
    });
  },
});

new Vue({
  router,
  store,
  render: h => h(App)
}).$mount('#app')