<template>
  <div class="groupReport">
    <el-card class="common-search-box">
      <h2 class="title">群报表</h2>
      <div class="common-search-container">
        <div class="search-item">
          <SearchDatePicker title="修改日期" :value="groupParams.groupDateRange" @searchDateChange="searchDateChange($event)">
          </SearchDatePicker>
        </div>
        <div class="search-item">
          <el-input class="group-inp" v-model="groupParams.groupOuterId"
            @input="groupParams.groupOuterId = groupParams.groupOuterId.trim()" placeholder="群id"></el-input>
          <el-input v-model="groupParams.groupKeyword" @input="groupParams.groupKeyword = groupParams.groupKeyword.trim()"
            placeholder="群名称(模糊搜索)"></el-input>
        </div>
        <div class="search-item search-btn">
          <el-button @click="queryGroupHandle" type="primary">查询</el-button>
          <el-button @click="reset">重置</el-button>
        </div>
      </div>
    </el-card>
    <el-card class="common-table-container">
      <div class="common-header-box">
        <h3>群聊总数：{{ total }}</h3>
      </div>
      <el-table ref="multipleTable" :data="tableData" border style="width: 100%" height="100%">
        <el-table-column v-for="item in tableColumn" :key="item.label" :label="item.label" :prop="item.prop"
          :min-width="item.width || 120">
        </el-table-column>
        <el-table-column label="操作" width="200px">
          <template slot-scope="scope">
            <el-button type="text" @click="editMemberInfo(scope.row.outerId)">成员编辑</el-button>
          </template>
        </el-table-column>
      </el-table>

      <div class="common-table-footer">
        <el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange"
          :current-page="groupParams.pageNum" :page-sizes="[10, 20, 30, 40]" :page-size="groupParams.pageSize"
          layout="total, sizes, prev, pager, next, jumper" :total="total">
        </el-pagination>
      </div>
    </el-card>
    <!-- 弹出层 -->
    <el-dialog v-if="dialogVisible" title=" 成员报表" top="20vh" :visible.sync="dialogVisible" width="70%"
      @close="closeDialog">
      <h2 class="title">群用户列表</h2>
      <div class="common-search-container dialog-search">
        <div class="search-item">
          <el-input class="group-inp" v-model="dialogParams.unionId"
            @input="dialogParams.unionId = dialogParams.unionId.trim()" placeholder="用户ID"></el-input>
          <el-input class="group-inp" v-model="dialogParams.nickName"
            @input="dialogParams.nickName = dialogParams.nickName.trim()" placeholder="用户名(模糊查询)"></el-input>
          <el-select v-model="dialogParams.status" placeholder="请选择状态">
            <el-option label="在线" :value="0"></el-option>
            <el-option label="离线" :value="1"></el-option>
          </el-select>
        </div>
        <div class="search-item search-btn">
          <el-button @click="dialogQueryHandle" type="primary">查询</el-button>
          <el-button @click="dialogReset">重置</el-button>
        </div>
      </div>
      <el-table ref="dialogTable" :data="dialogTableData" border style="width: 100%" height="100%">
        <el-table-column v-for="item in dialogTableColumn" :key="item.label" :label="item.label" :prop="item.prop"
          :min-width="item.width || 120">
          <template slot-scope="scope">
            <div v-if="item.prop == 'status'">{{ numToMap[scope.row.status] }}</div>
            <div v-else>{{ scope.row[item.prop] }}</div>
          </template>
        </el-table-column>
        <el-table-column label="操作" width="200px">
          <template slot-scope="scope">
            <el-popconfirm confirm-button-text='确认' cancel-button-text='取消' icon="el-icon-info" icon-color="red"
              @confirm="kickMember(scope.row.id)" title="确认踢除？">
              <el-button slot="reference" size="small" type="primary">踢人</el-button>
            </el-popconfirm>
            <!-- <el-button size="small" type="primary" @click="kickMember(scope.row.id)">踢人</el-button> -->
          </template>
        </el-table-column>
      </el-table>
      <div class="pagination-container">
        <el-pagination @size-change="handleSizeChangeDialog" @current-change="handleSizeChangeDialog"
          :current-page="dialogParams.pageNum" :page-sizes="[10, 20, 30, 40]" :page-size="dialogParams.pageSize"
          layout="total, sizes, prev, pager, next, jumper" :total="dialogTotal">
        </el-pagination>
      </div>
    </el-dialog>
  </div>
</template>

<script>
// import utils from "@/api/utils";
export default {
  name: "GroupReport",
  data() {
    return {
      groupParams: {
        groupDateRange: "",
        groupOuterId: null,
        groupKeyword: "",
        pageSize: 10,
        pageNum: 1,
      }, //群报表查询条件
      dialogParams: {
        unionId: "",
        nickName: "",
        status: null,
        pageSize: 10,
        pageNum: 1,
      }, //弹窗成员列表查询条件
      dialogVisible: false, //控制弹窗显示与隐藏
      dialogGroupId: "", //群ID
      tableData: [], //群列表数据
      dialogTableData: [], //成员编辑弹窗数据
      total: 0, //群报表总条数
      dialogTotal: 0, //成员列表总条数
      //账号状态判断
      numToMap: {
        "0": '在线',
        "1": '离线'
      },
      //群列表表头
      tableColumn: [
        { prop: "outerId", label: "群ID", width: 150 },
        { prop: "outerName", label: "群昵称", width: 200 },
        { prop: "redEnvelopeSendStatistics", label: "发包数" },
        { prop: "redEnvelopeSendMoneyStatistics", label: "发包额" },
        { prop: "redEnvelopeSendPeopleStatistics", label: "发包人" },
        { prop: "memberNum", label: "总人数" },
        { prop: "onlinePeopleStatistics", label: "在线数" },
        { prop: "updateTime", label: "群修改日期", width: 200 },
        { prop: "updateTime", label: "统计时间", width: 200 }
      ],
      //弹出框成员列表表头
      dialogTableColumn: [
        { prop: "unionId", label: "用户ID" },
        { prop: "nickName", label: "用户名" },
        { prop: "mobile", label: "手机号码" },
        { prop: "invitationUnionId", label: "邀请ID" },
        { prop: "invitationNickName", label: "邀请昵称" },
        { prop: "status", label: "状态" },
        { prop: "onlineTime", label: "在线时间", width: 200 },
      ],
    };
  },
  created() {
    // this.query();
    this.getGroupData(); //初始化群报表数据
  },
  methods: {
    // 获取群报表搜索的回传日期
    searchDateChange(val) {
      this.groupParams.groupDateRange = val;
    },
    // 获取弹窗成员列表搜索的回传日期
    dialogSearchDateChange(val) {
      this.dialogParams.dateRange = val;
    },
    // 踢出成员
    async kickMember(id) {
      let res = await this.$Axios.delete("im/group/user/" + id);
      let { code } = res.data;
      if (code === "000000") {
        this.$message.success('删除成功!');
        this.getMemberData(this.dialogGroupId); //重绘成员列表视图
        this.getGroupData(); //重绘群报表数据
      }
    },
    // 获取群报表数据
    async getGroupData() {
      let res = await this.$Axios.post("im/group/page", JSON.stringify({
        pageSize: this.groupParams.pageSize,
        pageNum: this.groupParams.pageNum,
        dateRange: this.groupParams.groupDateRange ? `${this.groupParams.groupDateRange[0]}~${this.groupParams.groupDateRange[1]}` : null,
        levelId: null,
        groupOuterId: this.groupParams.groupOuterId ? this.groupParams.groupOuterId : null,
        groupKeyword: this.groupParams.groupKeyword
      }), { contentType: 'application/json' });
      let { code, data } = res.data;
      if (code === "000000") {
        this.total = data.total;
        this.tableData = data.records;
      }
    },
    // 群报表表单查询
    queryGroupHandle() {
      if ((this.total / this.groupParams.pageSize) < this.groupParams.pageNum) { //判断所请求的数据页码与当前页码大小
        this.groupParams.pageNum = 1;
      }
      this.getGroupData(); //重绘群报表
    },
    // 群报表重置按钮
    reset() {
      this.groupParams.groupOuterId = null;
      this.groupParams.groupDateRange = "";
      this.groupParams.groupKeyword = "";
      this.queryGroupHandle(); //重绘群报表
    },
    // 成员列表表单搜索
    dialogQueryHandle() {
      this.getMemberData(this.dialogGroupId);
    },
    // 成员列表弹窗重置按钮
    dialogReset() {
      this.dialogParams.unionId = "";
      this.dialogParams.nickName = "";
      this.dialogParams.status = null;
      this.getMemberData(this.dialogGroupId);
    },
    // 获取成员列表
    async getMemberData(id) {
      let res = await this.$Axios.post("im/group/user-list", JSON.stringify({
        pageSize: this.dialogParams.pageSize,
        pageNum: this.dialogParams.pageNum,
        groupOuterId: id,
        unionId: this.dialogParams.unionId,
        nickName: this.dialogParams.nickName,
        status: this.dialogParams.status
      }), { contentType: 'application/json' })
      let { code, data } = res.data;
      if (code === "000000") {
        this.dialogTableData = data.records;
        this.dialogTotal = data.total;
      }
    },
    // 群报表成员编辑
    editMemberInfo(id) {
      this.dialogGroupId = id;
      this.getMemberData(this.dialogGroupId);
      this.dialogVisible = true;
      // let { nickName, headUrl, id } = row;
      // this.editForm.nickName = nickName;
      // this.editForm.headUrl = headUrl;
      // this.editForm.id = id;

    },
    // 群报表成员弹出框关闭的回调
    closeDialog() {
      this.dialogParams = {
        unionId: "",
        nickName: "",
        status: null,
        pageSize: 10,
        pageNum: 1,
      }
    },
    // 群报表当前页改变
    handleCurrentChange(val) {
      this.groupParams.pageNum = val;
      this.getGroupData();
    },
    // 群报表每页显示条数改变
    handleSizeChange(val) {
      this.groupParams.pageSize = val;
      this.getGroupData();
    },
    // 成员列表当前页改变
    handleCurrentChangeDialog(val) {
      this.dialogParams.pageNum = val;
      this.getMemberData(this.dialogGroupId);
    },
    // 成员列表每页显示条数改变
    handleSizeChangeDialog(val) {
      this.dialogParams.pageSize = val;
      this.getMemberData(this.dialogGroupId);
    }
  },
  computed: {},
};
</script>

<style lang="less" scoped>
.groupReport {

  // height: 100%;
  .title {
    padding-bottom: 10px;
    margin-bottom: 20px;
    border-bottom: 1px solid #ccc;
    color: rgb(129, 129, 129);
    font-size: 16px;
    font-weight: normal;
  }

  h3 {
    padding: 15px 5px;
    width: 100%;
    color: #444;
    background-color: #f2f2f2;
  }

  .el-form-item {
    margin-bottom: 5px;
  }

  .group-inp {
    width: 160px;
    margin-right: 15px;
  }

  /deep/ .el-form-item__label {
    display: block;
    width: 100%;
    text-align: left;
  }

  /deep/ .el-dialog {
    position: relative;
    margin-top: 10vh !important;
    min-height: 500px;

    .pagination-container {
      position: absolute;
      right: 40px;
      bottom: 40px;
    }

    .dialog-search {
      margin-bottom: 20px;
    }
  }

  /deep/ .el-dialog__header {
    padding: 10px 20px !important;
    background-color: #f2f2f2;

    .el-dialog__title {
      font-size: 14px;
    }
  }

  /deep/ .el-dialog__body {
    padding-bottom: 90px;
  }

  // .dialog-footer {
  //   .el-button {
  //     width: 100%;
  //   }
  // }
}
</style>