<template>
  <div>
    <el-upload ref="upload" :action="baseImgUrl" :headers="headers" :limit="limitNum" list-type="picture-card"
      :file-list="fileList" :on-success="handleAvatarSuccess" :on-change="handleChange" :on-preview="handlePreview"
      :on-remove="handleRemove" :on-error="handleError" :before-upload="beforeAvatarUpload" :class="{ hide: hideUpload }">
      <i class="el-icon-plus"></i>
    </el-upload>
    <el-dialog :visible.sync="dialogVisible" append-to-body>
      <img width="100%" :src="dialogImageUrl" alt="" />
    </el-dialog>
  </div>
</template>

<script>
export default {
  data() {
    return {
      baseImgUrl: process.env.VUE_APP_BASE_URL + '/file', // 上传的服务器地址
      headers: {
        Authorization: sessionStorage.accessToken,
      },
      imageUrl: "",
      fileList: [],
      hideUpload: false,
      dialogVisible: false,
      dialogImageUrl: "",
    };
  },
  props: {
    // 大小限制(MB)
    fileSize: {
      type: Number,
      default: 2,
    },
    // 限制上传个数
    limitNum: {
      type: Number,
      default: 1,
    },
    // 类型
    fileType: {
      type: String,
      default: "jpeg,gif,jpg,png",
    },
    echoImgUrl: {
      type: String,
      default: null
    }
  },
  methods: {
    handleChange(file, filelist) {
      this.hideUpload = filelist.length >= this.limitNum;
    },
    handlePreview(file) {
      console.log("Preview",file);
      this.dialogImageUrl = file.url;
      this.dialogVisible = true;
    },
    handleRemove(file, filelist) {
      this.hideUpload = filelist.length >= this.limitNum;
    },
    handleAvatarSuccess(res, file) {
      console.log(res, file, "look");
      console.log(file, "look2");
      if (res.data) {
        this.$emit("imageUrl", res.data);
      }
    },
    handleError(err, file) {
      // console.log(err,file);
      this.$message.error('上传失败，请稍后重试');
    },
    beforeAvatarUpload(file) {
      const isJPG = file.type.includes("image");
      const isLt20M = file.size / 1024 / 1024 < 20;
      if (!isJPG) {
        this.$message.error('上传图片只能是图片格式!');
      }
      if (!isLt20M) {
        this.$message.error('上传图片大小不能超过 20MB!');
      }
      return isJPG && isLt20M;
    },
  },
  mounted() {
    if (this.echoImgUrl) {
      this.fileList.push({ url: this.echoImgUrl });
    }
    this.hideUpload = this.fileList.length >= this.limitNum;
  },

};
</script>

<style lang="less" scoped>
/deep/ .hide .el-upload {
  display: none;
}
</style>