<template>
    <div class="tables">
        <div class="table-item">
            <h3>{{ titleText }}</h3>
            <el-table :data="tableParams.tableData" border style="width: 100%" height="100%">
                <el-table-column v-for="item in tableColumn" :key="item.label" :label="item.label" :prop="item.prop"
                    :min-width="item.width || 120">
                </el-table-column>
            </el-table>
            <!-- 分页器 -->
            <div class="pagination-container">
                <el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange"
                    :current-page="tableParams.pageNum" :page-sizes="[10, 20, 50, 100]"
                    :page-size="tableParams.pageSize" layout="total, sizes, prev, pager, next, jumper"
                    :total="tableParams.total">
                </el-pagination>
            </div>
        </div>
    </div>
</template>

<script>
export default {
    props: {
        // 标题
        titleText: {
            type: String,
            default: "",
        },
        // 表格所需数据
        tableParams: {
            type: Object,
            default: () => {
                return {
                    tableData: [],
                    pageNum: 1,
                    pageSize: 10,
                    total: 0
                }
            }
        },
        // 表格表头
        tableColumn: {
            type: Array,
            default: () => []
        }
    },
    data() {
        return {
        };
    },
    methods: {
        // 每页条数改变
        handleSizeChange(val) {
            this.$emit("sizeChange",  val);
        },
        // 当前页改变
        handleCurrentChange(val) {
            this.$emit("currentChange", val);
        },
    },
}


</script>

<style lang="less" scoped>
.tables {
    h3 {
        padding: 10px;
        width: 100%;
        margin-bottom: 10px;
        font-size: 14px;
        font-weight: bold;
        color: #444;
        background-color: #f2f2f2;
    }

    .table-item {
        margin-bottom: 20px;
    }

    .pagination-container {
        margin-top: 10px;
    }
}
</style>