<template>
  <div class="accountUpdate">
    <el-card class="common-table-container">
      <div class="common-header-box editInfo-headerBox">
        <h3>{{ titleName }}</h3>
      </div>
      <el-form
        ref="accountUpdate"
        :model="accountForm"
        :rules="rules"
        label-width="80px"
        class="main-box"
      >
        <el-form-item label="名称" prop="nickName">
          <el-input
            v-model="accountForm.nickName"
            placeholder="请填写名称"
          ></el-input>
        </el-form-item>
        <el-form-item label="账号" prop="userName">
          <el-input
            v-model="accountForm.userName"
            placeholder="请填写账号"
            :disabled="titleName != '添加账号'"
          ></el-input>
        </el-form-item>
        <el-form-item label="手机号" prop="mobile">
          <el-input
            v-model="accountForm.mobile"
            placeholder="请填写手机号"
            :disabled="titleName != '添加账号'"
          ></el-input>
        </el-form-item>
        <el-form-item label="密码" prop="password">
          <el-input
            v-model="accountForm.password"
            show-password
            placeholder="请填写密码"
          ></el-input>
        </el-form-item>
        <el-form-item label="权限" prop="roleId">
          <el-select v-model="accountForm.roleId" clearable placeholder="请选择">
            <el-option
              v-for="item in roleList"
              :key="item.id"
              :value="item.id"
              :label="item.appellation"
            >
            </el-option>
          </el-select>
        </el-form-item>
      </el-form>
      <div class="common-table-footer">
        <div class="button-box">
          <el-button @click="submitAccount($route.query.type)" type="primary">保存</el-button>
          <el-button
            @click="$router.replace({ name: 'AccountManage' })"
            >取消</el-button
          >
        </div>
      </div>
    </el-card>
  </div>
</template>

<script>
import { PASSWORD_REG } from "@/api/regular";
import MD5 from "crypto-js/md5";
import utils from "@/api/utils";
export default {
  name: "AccountUpdate",
  data() {
    let checkPwd = (rules, value, callback) => {
      if (!value) {
        callback(new Error("请输入密码"));
      } else if (!PASSWORD_REG(value)) {
        callback(new Error("请输入8~20位包含字母和数字的密码"));
      } else {
        callback();
      }
    };
    return {
      rules: {
        //表单验证规则
        nickName: [
          { required: true, message: "请输入名称", trigger: "blur" }
        ],
        userName: [
          { required: true, message: "请输入账号", trigger: "blur" },
        ],
        mobile: [
          { required: true, message: "请输入手机号",min: 11, max: 11, trigger: "blur" },
        ],
        roleId: [
          { required: true, message: "请选择权限", trigger: "blur" }
        ],
        password: [
          { required: true, validator: checkPwd, trigger: "blur" },
        ],
      },
      params: {
        pageNum: 1,
        pageSize: 10
      },
      titleName: "添加账号",
      accountForm: {
        nickName: "",
        userName: "",
        password: "",
        roleId: "",
      },
      roleList: [],
    };
  },
  created() {
    this.queryRole();
    this.titleName = this.$route.query.type === "add" ? "添加账号" : "编辑账号";
    this.accountForm.nickName = this.$route.query.nickName ?  this.$route.query.nickName : "";
    this.accountForm.userName = this.$route.query.userName ?  this.$route.query.userName : "";
    this.accountForm.roleId = this.$route.query.roleId ?  String(this.$route.query.roleId) : ""
  },
  methods: {
    queryRole() {
      this.$Axios.post('/role/page',JSON.stringify(this.params),{contentType: 'application/json'})
      .then( res=> {
        if( res.data.data ) {
          this.roleList = res.data.data.records;
        }
      }).catch( err=>console.log(err))
    },
    submitAccount(type) {
      this.$refs.accountUpdate.validate(async (vali) => {
        if(vali) {
          let params = utils.objDeleteNull({...this.accountForm})
          if( type == 'add' ) {
            this.$Axios.post('/user', JSON.stringify(params),{contentType: 'application/json'})
            .then( res=> {
              if( res.data.data ) {
                this.$message.success('添加成功')
                this.$router.replace({ name: 'AccountManage'})
              } 
            }).catch( err=>console.log(err))
          }else {
            params.id = this.$route.query.id ?  String(this.$route.query.id) : null;
            this.$Axios.put('/user', JSON.stringify(params),{contentType: 'application/json'})
            .then( res=> {
              if( res.data.data ) {
                this.$message.success('编辑成功')
                this.$router.replace({ name: 'AccountManage'})
              } 
            }).catch( err=>console.log(err))
          }
        }else {
          return;
        }
      })
      
    }
  }
};
</script>

<style lang="less" scoped>
.accountUpdate {
  // height: 100%;
  .main-box {
    .el-input {
      width: 35%;
    }
  }
}
</style>