<template>
  <div class="editInfo">
    <el-card class="common-table-container">
      <div class="common-header-box editInfo-headerBox">
        <h3>修改资料</h3>
      </div>
      <el-form ref="editInfo" :rules="rules" :model="editInfo" label-width="80px" class="main-box">
        <el-form-item label="名称" prop="nickName">
          <el-input v-model="editInfo.nickName" placeholder="请填写名称"></el-input>
        </el-form-item>
        <el-form-item label="账号" prop="userName">
          <el-input v-model="editInfo.userName" disabled placeholder="请填写名称"></el-input>
        </el-form-item>  
        <el-form-item label="密码" prop="password">
          <el-input v-model="editInfo.password" type="password" show-password placeholder="请填写密码"></el-input>
        </el-form-item> 
      </el-form>
      <div class="common-table-footer">
        <div class="button-box">
            <el-button @click="submitUserInfo" type="primary">保存</el-button>
            <el-button @click="$router.go(-1)">取消</el-button>
        </div>
      </div>
    </el-card>
  </div>
</template>

<script>
import { PASSWORD_REG } from "@/api/regular";
import MD5 from "crypto-js/md5";
export default {
  name: "EditInfo",
  data() {
    let checkPwd = (rules, value, callback) => {
      if (!value) {
        callback(new Error("请输入密码"));
      } else if (!PASSWORD_REG(value)) {
        callback(new Error("请输入8~20位包含字母和数字的密码"));
      } else {
        callback();
      }
    };
    return {
      editInfo: {
        nickName: '',
        userName: '',
        password: '',
      },
      rules: {
        //表单验证规则
        nickName: [
          { required: true, message: "请输入名称", trigger: "blur" }
        ],
        userName: [
          { required: true, message: "请输入账号", trigger: "blur" }
        ],
        password: [
          { required: true, validator: checkPwd, trigger: "blur" }
        ],
      },
    };
  },
  created() {
    let params = JSON.parse(sessionStorage.accountInfo);
    let {nickName, userName} = params;
    this.editInfo.nickName = nickName;
    this.editInfo.userName = userName;
  },
  methods: {
    submitUserInfo() {
       this.$refs.editInfo.validate(async (vali) => {
         if(vali) {
           let params = { ...this.loginForm };
           params.id = JSON.parse(sessionStorage.accountInfo).id;
           this.$Axios.put('/user',JSON.stringify(params),{contentType: 'application/json'})
           .then( res=> {
             if( res.data.data ) {
               this.$message.success('修改成功，请重新登录');
               setTimeout(() => {
                 this.$router.replace('/login');
               }, 1500);
             }
           }).catch( err=>console.log(err))
         } else {
           return;
         }
       })
    }
  },
};
</script>

<style lang="less" scoped>
.editInfo {
  // height: 100%;
  .main-box {
    
    .el-input {
        width: 35%;
    }
 
  }
}
</style>