<template>
  <div class="userList">
    <el-card class="common-search-box">
      <div class="common-search-container">
        <div class="search-item">
          <SearchDatePicker
            title="注册日期"
            :value="params.dateRange"
            @searchDateChange="searchDateChange($event)"
          ></SearchDatePicker>
        </div>
        <div class="search-item">
          <span class="title">搜索用户 </span>
          <el-input
            suffix-icon="el-icon-search"
            v-model="params.userKeyword"
            @input="params.userKeyword = params.userKeyword.trim()"
            placeholder="请输入名称、手机号、ID搜索"
          ></el-input>
        </div>
        <div class="search-item search-btn">
          <el-button @click="query('query')" type="primary">查询</el-button>
          <el-button @click="reset">重置</el-button>
        </div>
      </div>
    </el-card>
    <el-card class="common-table-container">
      <div class="common-header-box">
        <h3>用户列表</h3>
      </div>
      <el-table
        ref="multipleTable"
        :data="tableData"
        @select="handleSelectionChange"
        @select-all="handleSelectionChange"
        border
        style="width: 100%"
        height="100%"
      >
        <el-table-column type="selection" width="55"></el-table-column>
        <el-table-column
          v-for="item in tableColumn"
          :key="item.label"
          :label="item.label"
          :prop="item.prop"
          :min-width="item.width || 120"
        >
          <template slot-scope="scope">
            <el-switch v-if="item.prop == 'status'" v-model="scope.row.status" @change="isOpenUser(scope.row)">
            </el-switch>
            <div v-else-if="item.prop == 'headUrl'">
              <img
                :src="scope.row.headUrl"
                v-if="scope.row.headUrl"
                v-imgview
              />
              <span v-else>暂无图片</span>
            </div>
            <div v-else>{{ scope.row[item.prop] }}</div>
          </template>
        </el-table-column>
        <el-table-column label="操作" width="200px">
          <template slot-scope="scope">
            <el-button type="text" @click="editUserInfo(scope.row)"
              >编辑</el-button
            >
          </template>
        </el-table-column>
      </el-table>

      <div class="common-table-footer">
        <div>
          <el-checkbox
            v-model="checkAll"
            @change="checkAllChange"
            style="margin-right: 30px"
            >全选</el-checkbox
          >
          <el-button @click="exportList">导出所选</el-button>
          <el-button @click="exportAllList">导出全部</el-button>
        </div>
        <el-pagination
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
          :current-page="params.pageNum"
          :page-sizes="[10, 20, 30, 40]"
          :page-size="params.pageSize"
          layout="total, sizes, prev, pager, next, jumper"
          :total="total"
        >
        </el-pagination>
      </div>
    </el-card>
    <el-dialog v-if="dialogVisible" title="编辑用户信息" :visible.sync="dialogVisible" width="20%">
      <el-form :model="editForm">
        <el-form-item label="用户头像：">
          <div>
            <UploadHead @imageUrl="getImageUrl" :echoImgUrl="editForm.headUrl"></UploadHead>
          </div>
        </el-form-item>
        <el-form-item label="用户昵称：">
          <el-input v-model="editForm.nickName"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button type="primary" @click="submitUserInfo"
          >确 定</el-button
        >
      </span>
    </el-dialog>
  </div>
</template>

<script>
import UploadHead from "@/components/UploadHead.vue";
import utils from "@/api/utils";
export default {
  name: "UserList",
  components: {
    UploadHead,
  },
  data() {
    return {
      imageList: [],
      params: {
        userKeyword: "",
        dateRange: "",
        pageSize: 10,
        pageNum: 1,
      },
      params2: {
        userKeyword: "",
        dateRange: "",
        pageSize: 10,
        pageNum: 1,
      },
      editForm: {
        nickName: null,
        headUrl: null,
        id: null,
      },
      formLabelWidth: "120px",
      dialogVisible: false,
      tableData: [],
      total: 0,

      tableColumn: [
        { prop: "id", label: "ID", width: 150 },
        { prop: "headUrl", label: "头像" },
        { prop: "nickName", label: "昵称" },
        { prop: "mobile", label: "手机号码" },
        { prop: "regTime", label: "注册时间", width: 200 },
        { prop: "status", label: "启用", width: 80 },
      ],
      // 选中全部数据，默认不选中
      checkAll: false,
      // 选中数据
      selectData: [],
    };
  },
  created() {
    this.query();
    
  },
  methods: {
    // 获取回传图片
    getImageUrl(val) {
      this.editForm.headUrl = val.url;
    },
    // 获取回传日期
    searchDateChange(val) {
      this.params.dateRange = val;
    },
    // 查询
    query(type) {
      if (type == "query") {
        this.params.pageNum = 1;
        this.params2 = JSON.parse(JSON.stringify(this.params));
      } else {
        this.params2.pageNum = this.params.pageNum;
        this.params2.pageSize = this.params.pageSize;
      }
      let params = JSON.parse(
        JSON.stringify(utils.objDeleteNull(this.params2))
      );
      params.dateRange = this.params2.dateRange
        ? this.params2.dateRange.join(" ~ ")
        : null;
      this.$Axios
        .post("/user/page", JSON.stringify(params),{contentType: 'application/json'})
        .then((res) => {
          if( res.data.data ) {
            this.tableData = res.data.data.records;
            this.tableData.forEach((item) => {
              item.status = item.status == 1;
            });
            this.total = res.data.data.total;
          }
        })
        .catch((err) => {
          console.log(err);
        });
    },
    // 重置按钮
    reset() {
      this.params.userKeyword = null;
      this.params.dateRange = null;
      this.query("query");
    },
    // 编辑
    editUserInfo(row) {
      let { nickName, headUrl, id } = row;
      this.editForm.nickName = nickName;
      this.editForm.headUrl = headUrl;
      this.editForm.id = id;
      
      this.dialogVisible = true;
    },
    // 启用
    isOpenUser(row) {
      let params = {
        id: row.id,
        status: row.status ? 1 : 3
      }
      this.$Axios.put('/user/status',params)
      .then( res=> {
        if( res.data.data) {
          this.$message.success('操作成功');
          this.query();
        }
      }).catch( err=> {
        console.log(err);
      })

    },
    submitUserInfo() {
      if(utils.isEmpty(this.editForm.nickName)) {
        this.$message.warning('请填写用户昵称')
        return;
      }
      let params = {...this.editForm}
      this.$Axios.put('/user',JSON.stringify(params),{contentType: 'application/json'})
      .then( res=> {
        if(res.data.data) {
          this.$message.success('编辑成功');
          this.query();
          this.dialogVisible = false;
        }
      })
    },

    // 导出所选
    exportList() {
      if (this.selectData.length > 0) {
        let ids = utils.arrayOpera(this.selectData, "id");
        let params = {
          ids,
          userKeyword: this.params2.userKeyword,
          dateRange: this.params2.dateRange
            ? this.params2.dateRange.join(" ~ ")
            : null,
        };
        this.$Axios
          .post("/user/export", JSON.stringify(params), {
            responseType: "blob", contentType: 'application/json'
          })
          .then(data => {
            if(data) {
              utils.excelDownload(data, "导出用户列表.xls");
            }
          })
          .catch((err) => {
            console.log(err);
          });
      } else {
        this.$message.warning("请选择导出项");
        return;
      }
    },
    // 导出全部
    exportAllList() {
      let params = {
        userKeyword: this.params2.userKeyword,
        dateRange: this.params2.dateRange
          ? this.params2.dateRange.join(" ~ ")
          : null,
      };
      this.$Axios
        .post("/user/export", JSON.stringify(params), {
          responseType: "blob", contentType: 'application/json'
        })
        .then(data => {
          if( data ) {
            utils.excelDownload(data, "导出全部用户列表.xls");
          }
        })
        .catch((err) => {
          console.log(err);
        });
    },
    // 当前页改变
    handleCurrentChange(page) {
      this.params.pageNum = page;
      this.checkAll = false;
      this.query();
    },
    // 每页显示条数改变
    handleSizeChange(val) {
      this.params.pageSize = val;
      this.checkAll = false;
      this.query();
    },
    // 选项改变时
    handleSelectionChange(val) {
      this.selectData = val;
      this.checkAll = val.length == this.tableData.length;
    },
    // 选中全部数据
    checkAllChange(val) {
      if (val) {
        // this.tableData.forEach((row) => {
        //   this.$refs.multipleTable.toggleRowSelection(row, true);
        // });
        this.$refs.multipleTable.toggleAllSelection();
      } else {
        this.$refs.multipleTable.clearSelection();
        this.selectData = [];
      }
    },
  },
  computed: {},
};
</script>

<style lang="less" scoped>
.userList {
  // height: 100%;
  .el-form-item {
    margin-bottom: 5px;
  }
  /deep/ .el-form-item__label {
    display: block;
    width: 100%;
    text-align: left;
  }
  .dialog-footer {
    .el-button {
      width: 100%;
    }
  }
}
</style>