<template>
  <div class="login">
    <el-card class="login-box">
      <h1>IM管理后台</h1>
      <h4>欢迎登入</h4>
      <el-form
        :model="loginForm"
        :rules="rules"
        status-icon
        ref="loginForm"
        style="width: 70%; margin: 0 auto"
      >
        <el-form-item prop="userName">
          <el-input v-model="loginForm.userName" placeholder="用户名"></el-input>
        </el-form-item>
        <el-form-item prop="password">
          <el-input
            v-model="loginForm.password"
            show-password
            placeholder="密码"
          ></el-input>
        </el-form-item>
        <el-form-item>
          <el-button style="width: 100%" type="primary" @click="loginSubmit"
            >登 录</el-button
          >
        </el-form-item>
      </el-form>
    </el-card>
  </div>
</template>

<script>
import { PASSWORD_REG } from "@/api/regular";
// import MD5 from "crypto-js/md5";
export default {
  name: "Login",

  data() {
    let checkPwd = (rules, value, callback) => {
      if (!value) {
        callback(new Error("请输入密码"));
      } else if (!PASSWORD_REG(value)) {
        callback(new Error("请输入8~20位包含字母和数字的密码"));
      } else {
        callback();
      }
    };
    return {
      loginForm: {
        userName: "",
        password: ""
      },
      rules: {
        //表单验证规则
        userName: [
          { required: true, message: "请输入用户名", trigger: "blur" },
        ],
        password: [{ required: true, validator: checkPwd, trigger: "blur" }]
      }
    };
  },
  created() {},
  methods: {
    loginSubmit() {
      this.$refs.loginForm.validate(async vali => {
        if (vali) {
          let params = { ...this.loginForm };
          // params.password = MD5(this.loginForm.password).toString();
          this.$Axios
            .post("/user/sign-in", JSON.stringify(params), {
              contentType: "application/json"
            })
            .then(res => {
              if (res.data.data) {
                // 获取用户信息
                let accessToken = res.data.data.accessToken;
                let tokenType =
                  res.data.data.tokenType[0].toUpperCase() +
                  res.data.data.tokenType.slice(1);
                //存储token
                sessionStorage.setItem(
                  "accessToken",
                  tokenType + " " + accessToken
                );
                // vuex存储
                this.$store.commit(
                  "ACCOUNTTOKEN",
                  tokenType + " " + accessToken
                );
                this.getUserInfo();
                this.getRoleList();
              }
            })
            .catch(err => {
              console.log(err);
            });
        } else {
          return;
        }
      });
    },
    getUserInfo() {
      this.$Axios
        .get("/user/current-sign-in")
        .then(res => {
          if (res.data.data) {
            let data = res.data.data;
            sessionStorage.setItem("accountInfo", JSON.stringify(data));
            this.$store.commit("ACCOUNTINFO", data);
          }
        })
        .catch(err => {
          console.log(err);
        });
    },
    getRoleList() {
      this.$Axios
        .get("/user/menu-list")
        .then(res => {
          if (res.data.data) {
            res.data.data.forEach(item => {
              if (item.children && item.children.length < 1 && item.linkUrl) {
                item.index = item.linkUrl;
              } else {
                item.index = item.appellation;
              }
            });
            sessionStorage.setItem("roleMenu", JSON.stringify(res.data.data));
  
            // 跳转
            let data = res.data.data;
            if( data[0].children &&  data[0].children.length >0) {
              setTimeout(() => {
                this.$router.push(data[0].children[0].linkUrl)
              }, 1000);
            }else {
              setTimeout(() => {
                this.$router.push(data[0].linkUrl)
              }, 1000);
            }
            this.$message.success("登录成功");
          }
        })
        .catch(err => console.log(err));
    }
  }
};
</script>

<style lang="less" scoped>
.login {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
  background-color: #f3f3f4;
  .login-box {
    width: 589px;
    padding: 20px 0;
    text-align: center;
    margin-bottom: 10vh;
    box-shadow: 0 0 20px rgba(170, 170, 170, 35%) !important;
    h1 {
      color: rgb(230, 230, 230);
      font-size: 36px;
      &:hover {
        color: rgb(27, 179, 147);
      }
    }
    h4 {
      color: rgb(141, 143, 145);
      font-weight: 700;
      font-size: 18px;
      margin: 25px 0;
    }
  }
}
</style>
