<template>
  <div class="account-Change">
    <el-card class="common-search-box">
      <h2 class="title">账变信息</h2>
      <div class="common-search-container">
        <div class="search-item">
          <el-input class="item" v-model="params.unionId" @input="params.unionId = params.unionId.trim()"
            placeholder="用户ID">
          </el-input>
          <el-select v-model="params.typeId" placeholder="账变类型">
            <el-option v-for="item in X_getWalletChangeList" :key="item.id" :label="item.typeName" :value="item.id">
            </el-option>
          </el-select>
          <el-input class="item" v-model="params.fundSource" @input="params.fundSource = params.fundSource.trim()"
            placeholder="账变对象(数字)"></el-input>
          <div class="search-item date-picker">
            <SearchDatePicker title="选择时间段" :value="params.createTimeArr" @searchDateChange="searchDateChange($event)">
            </SearchDatePicker>
          </div>
          <el-input class="item" v-model="params.minAmount" @input="params.minAmount = params.minAmount.trim()"
            placeholder="最小金额">
          </el-input>
          <el-input class="item" v-model="params.maxAmount" @input="params.maxAmount = params.maxAmount.trim()"
            placeholder="最大金额">
          </el-input>
        </div>
        <div class="search-item search-btn">
          <el-button @click="getData" type="primary">查询</el-button>
          <el-button @click="reset()">重置</el-button>
        </div>
      </div>
    </el-card>
    <el-card class="common-table-container">
      <el-table ref="multipleTable" :data="tableData" border style="width: 100%" height="100%">
        <el-table-column v-for="item in tableColumn" :key="item.label" :label="item.label" :prop="item.prop"
          :min-width="item.width || 120">
        </el-table-column>
      </el-table>

      <div class="common-table-footer">
        <el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange"
          :current-page="params.pageNum" :page-sizes="[10, 20, 30, 40]" :page-size="params.pageSize"
          layout="total, sizes, prev, pager, next, jumper" :total="total">
        </el-pagination>
      </div>
    </el-card>
  </div>
</template>

<script>
// import utils from "@/api/utils";
export default {
  name: "AccountChangeInfo",
  data() {
    return {
      params: {
        pageSize: 10,
        pageNum: 1,
        unionId: "",
        typeId: null,
        fundSource: null,
        createTimeArr: null,
        minAmount: null,
        maxAmount: null
      },
      tableData: [], //列表数据
      total: 0, //总条数
      tableColumn: [
        { prop: "unionId", label: "用户ID" },
        { prop: "nickName", label: "昵称" },
        { prop: "typeName", label: "账变类型" },
        { prop: "fundSource", label: "账变对象" },
        { prop: "preBalance", label: "前余额" },
        { prop: "afterBalance", label: "后余额" },
        { prop: "changeAmount", label: "变动余额" },
        { prop: "createTime", label: "时间", width: 200 },
        { prop: "remark", label: "备注" }
      ], //账变信息表头
    };
  },
  created() {
    this.$store.dispatch("A_getWalletChangeList"); //获取账变类型列表
    this.pageWalletChange(); //初始化账变信息数据
  },
  computed: {
    // 获取账变类型列表
    X_getWalletChangeList() {
      return this.$store.state.walletChangeList;
    },
  },
  methods: {
    // 条件分页查询账变列表
    async pageWalletChange() {
      let res = await this.$Axios.post("wallet-change/page", JSON.stringify({
        pageSize: this.params.pageSize,
        pageNum: this.params.pageNum,
        unionId: this.params.unionId,
        typeId: this.params.typeId,
        fundSource: this.params.fundSource ? this.params.fundSource : null,
        createTimeArr: this.params.createTimeArr,
        minAmount: this.params.minAmount ? this.params.minAmount : null,
        maxAmount: this.params.maxAmount ? this.params.maxAmount : null,
      }), { contentType: 'application/json' })
      let { code, data } = res.data;
      if (code === "000000") {
        this.total = data.total;
        this.tableData = data.records;
      }
    },
    // 表单搜索查询
    getData() {
      if ((this.total/this.params.pageSize)<this.params.pageNum) { //判断所请求的数据页码与当前页码大小
        this.params.pageNum = 1;
      }
      this.pageWalletChange(); //重绘账变信息数据
    },
    // 获取搜索的回传日期
    searchDateChange(val) {
      this.params.createTimeArr = val;
    },
    // 重置按钮
    reset() {
      this.params.unionId = "";
      this.params.typeId = null;
      this.params.fundSource = null;
      this.params.createTimeArr = null;
      this.params.minAmount = null;
      this.params.maxAmount = null;
      this.pageWalletChange(); //重绘账变信息数据
    },
    // 当前页改变
    handleCurrentChange(val) {
      this.params.pageNum = val;
      this.pageWalletChange(); //重绘账变信息数据
    },
    // 每页显示条数改变
    handleSizeChange(val) {
      this.params.pageSize = val;
      this.pageWalletChange(); //重绘账变信息数据
    }
  }
};
</script>

