<template>
  <div class="accountManage">
    <el-card class="common-table-container">
      <div class="common-header-box">
        <h3>账号列表</h3>
        <el-button type="primary" @click="$router.push({name: 'AccountUpdate', query:{type: 'add'}})">添加账号</el-button>
      </div>
      <el-table
        ref="multipleTable"
        :data="tableData"
        @select="handleSelectionChange"
        @select-all="handleSelectionChange"
        border
        height="100%"
      >
        <el-table-column type="selection" width="55"></el-table-column>
        <el-table-column
          v-for="item in tableColumn"
          :key="item.label"
          :label="item.label"
          :prop="item.prop"
          :min-width="item.width || 160"
        >
          <template slot-scope="scope">
            <el-switch v-if="item.prop === 'status'" v-model="scope.row.status" @change="isOpenAccount(scope.row)">
            </el-switch>
            <div v-else>{{ scope.row[item.prop]}}</div> 
          </template>
        </el-table-column>
        <el-table-column label="操作" width="200px">
          <template slot-scope="scope">
            <el-button type="text" @click="delData(scope.row)">删除</el-button>
            <el-button type="text" @click="$router.push({name: 'AccountUpdate', query:{type: 'edit', id: scope.row.id, nickName: scope.row.nickName, userName: scope.row.userName, roleId: scope.row.roleId }})">编辑</el-button>
          </template>
        </el-table-column>
      </el-table>
      <div class="common-table-footer">
        <div></div>
        <el-pagination
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
          :current-page="params.pageNum"   
          :page-sizes="[10, 20, 30, 40]"
          :page-size="params.pageSize"
          layout="total, sizes, prev, pager, next, jumper"
          :total="total"
        >
        </el-pagination>
      </div>
    </el-card>
  </div>
</template>

<script>
export default {
  name: "AccountManage",
  data() {
    return {
      params: {
        isAdmin: true,
        pageSize: 10,
        pageNum: 1,
      },
      total: 0,
      tableData: [], 
      tableColumn: [
        { prop: "id", label: "ID", },
        { prop: "nickName", label: "名称"},
        { prop: "userName", label: "账号" },
        { prop: "roleAppellation", label: "角色" },
        { prop: "regTime", label: "最后登录时间" },
        { prop: "status", label: "启用" },
      ],
    };
  },
  created() {
    this.getAccountList()
  },
  methods: {
    getAccountList() {
      this.$Axios.post('/user/page',JSON.stringify(this.params),{contentType: 'application/json'})
      .then( res=> {
        if( res.data.data ) {
          console.log(res.data);
          this.tableData = res.data.data.records;
          this.tableData.forEach((item) => {
            item.status = item.status == 1;
          });
          this.total = res.data.data.total;
        }
      }).catch( err=>console.log(err))
    },
    handleSelectionChange() {},

    // 删除
    delData(row) {
      this.$confirm("确定要删除?", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning",
      })
        .then(() => {
          this.$Axios.delete('/user/'+ row.id)
          .then( res=> {
            if( res.data.data ) {
              this.$message.success('删除成功');
              this.getAccountList();
            }
          }).catch( err=> console.log(err))
          
        })
        .catch(() => {
          console.log('已取消');
        });
    },
    isOpenAccount(row) {
      let params = {
        id: row.id,
        status: row.status ? 1 : 3
      }
      this.$Axios.put('/user/status',params)
      .then( res=> {
        if( res.data.data) {
          this.$message.success('操作成功');
          this.getAccountList();
        }
      }).catch( err=> {
        console.log(err);
      })
    },
    handleCurrentChange(page) {
      this.params.pageNum = page;
      this.getAccountList();
    },  
    handleSizeChange(val) {
      this.params.pageSize = val;
      this.getAccountList();
    },
  },
};
</script>

<style lang="less" scoped>
.accountManage {
  // height: 100%;
}
</style>