<template>
  <div id="editor"></div>
</template>

<script>
import E from "wangeditor";
export default {
  props: {
    content: {
      type: String,
      default: "",
    },

    // 图片大小
    imageSize: {
      type: Number,
      default: 2,
    },
    // 图片数量
    // imageNum: {
    //   type: Number,
    //   default: 5,
    // },
  },
  data() {
    return {
      editor: "",
    };
  },
  mounted() {
    this.editor = new E("#editor");

    // 配置全屏功能按钮是否展示
    this.editor.config.showFullScreen = false;

    // 配置onchange回调函数,获取输入的内容
    this.editor.config.onchange = (newHtml) => {
      console.log("change后最新的newHtml", newHtml);
      this.$emit("changeEditor", newHtml);
    };

    // 配置触发onchange时间频率，默认200ms
    this.editor.config.onchangeTimeout = 1;

    this.editor.config.uploadImgMaxSize = this.imageSize * 1024 * 1024; // 2M

    this.editor.config.uploadImgAccept = ["jpg", "jpeg", "png", "gif", "bmp"];

    this.editor.config.uploadImgMaxLength = 1; // 一次最多上传 1 个图片

    // this.editor.config.uploadImgShowBase64 = true; // base 64 存储图片

    this.editor.config.uploadFileName = "file"; // 后端接受上传文件的参数名

    this.editor.config.showLinkImg = false; // 隐藏网络上传图片

    this.editor.config.uploadImgHeaders = {
      Authorization: sessionStorage.accessToken,
    };

    this.editor.config.uploadImgServer =
      process.env.VUE_APP_BASE_URL + "/api/rest/file"; // 上传服务器地址

    this.editor.config.uploadImgHooks = { // 上传图片回调函数
      // 上传图片之前
      before: function (xhr) {
        console.log(xhr);

        // 可阻止图片上传
        return {
          prevent: false,
          msg: "需要提示给用户的错误信息",
        };
      },
      // 图片上传并返回了结果，图片插入已成功
      success: function (xhr) {
        console.log("success", xhr);
      },
      // 图片上传并返回了结果，但图片插入时出错了
      fail: function (xhr, editor, resData) {
        console.log("fail", resData);
      },
      // 上传图片出错，一般为 http 请求的错误
      error: function (xhr, editor, resData) {
        console.log("error", xhr, resData);
      },
      // 上传图片超时
      timeout: function (xhr) {
        console.log("timeout");
        this.$message.waring('上传超时，请稍后重试')
      },
      // 图片上传并返回了结果，想要自己把图片插入到编辑器中
      // 例如服务器端返回的不是 { errno: 0, data: [...] } 这种格式，可使用 customInsert
      customInsert: function (insertImgFn, result) {
        // result 即服务端返回的接口
        console.log("customInsert", result);

        // insertImgFn 可把图片插入到编辑器，传入图片 src ，执行函数即可
        // insertImgFn(result.data[0]);
        insertImgFn(result.data.url);
      },
    };
    this.editor.config.customAlert =  (str) => {
      this.$message.error(str);
    }

    this.editor.config.menus = [
      "head",
      "bold",
      "fontSize", // 字号
      "fontName", // 字体
      "italic",
      "underline",
      "strikeThrough", // 删除线
      "foreColor", // 文字颜色
      "backColor", // 背景颜色
      "link", // 插入链接
      "list", // 列表
      "justify", // 对齐方式
      "quote", // 引用
      "emoticon", // 表情
      "image", // 插入图片
      "table", // 表格
      "video", // 插入视频
      "code", // 插入代码
      "undo", // 撤销
      "redo", // 重复
    ];
    this.editor.create();
  },
  watch: {
    content() {
      this.editor.txt.html(this.content);
    },
  },
};
</script>

