<template>
  <div class="batch-add-friend">
    <el-card class="common-table-container">
      <div class="common-header-box">
        <h3>批量加好友</h3>
      </div>
      <main>
        <el-form :model="ruleForm" :rules="rules" ref="ruleForm" label-width="120px" class="demo-ruleForm">
          <el-form-item label="手机号1：" prop="fromMobile">
            <el-input v-model="ruleForm.fromMobile" clearable></el-input>
          </el-form-item>
          <el-form-item label="手机号2：" prop="mobiles">
            <el-input type="textarea" rows="15" v-model="ruleForm.mobiles" clearable></el-input>
            <p class="tips">* 多个手机号之间以英文逗号分割，上限50个</p>
          </el-form-item>
          <el-form-item>
            <el-button type="primary" @click="submitForm('ruleForm')">提交</el-button>
            <el-button @click="resetForm('ruleForm')">重置</el-button>
          </el-form-item>
        </el-form>
      </main>
      <div class="common-table-footer">


      </div>
    </el-card>
  </div>
</template>

<script>
export default {
  name: "BatchAddFriend",
  data() {
    return {
      ruleForm:{
        fromMobile:"",
        mobiles:""
      },
      rules: {
        fromMobile: [
          { required: true, message: '请输入手机号1', trigger: 'blur' },
        ]
      }
    };
  },
  created() {
  },
  methods: {
    // 批量添加好友
    async batchAddFriend() {
      let res = await this.$Axios.post("user/batch-add-friend", JSON.stringify(this.ruleForm), { contentType: 'application/json' });
      let { code } = res.data;
      if (code === "000000") {
        this.$message.success("添加成功！")
      }
    },
    // 表单提交
    submitForm(formName){
      this.$refs[formName].validate((valid) => {
          if (valid) {
            this.batchAddFriend(); // 批量添加好友
            this.$refs[formName].resetFields(); //重置表单
          } 
        });
    },
    // 重置表单
    resetForm(formName){
      this.$refs[formName].resetFields();
    }
  },
};
</script>

<style lang="less" scoped>
.batch-add-friend {
  main{
    width: 50%;
    .tips{
      font-size: 12px;
      color: rgb(235, 4, 4);
    }
  }
}
</style>