<template>
  <div class="argeeManage">
    <el-card class="common-table-container">
      <div class="common-header-box">
        <h3>协议列表</h3>
      </div>
      <el-table
        ref="multipleTable"
        :data="tableData"
        @select="handleSelectionChange"
        @select-all="handleSelectionChange"
        border
        height="100%"
      >
        <el-table-column type="selection" width="55"></el-table-column>
        <el-table-column
          v-for="item in tableColumn"
          :key="item.label"
          :label="item.label"
          :prop="item.prop"
          :min-width="item.width || 160"
        ></el-table-column>
        <el-table-column label="操作" min-width="200px">
          <template slot-scope="scope">
            <el-button type="text" @click="editData(scope.row)"
              >修改</el-button
            >
            <el-button type="text" @click="delData(scope.row)"
              >删除</el-button
            >
          </template>
        </el-table-column>
      </el-table>
      <div class="common-table-footer">
        <div></div>
        <el-pagination
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
          :current-page="params.pageNum"
          :page-sizes="[10, 20, 30, 40]"
          :page-size="params.pageSize"
          layout="total, sizes, prev, pager, next, jumper"
          :total="total"
        >
        </el-pagination>
      </div>
    </el-card>
    <el-dialog
      title="修改协议"
      :visible.sync="dialogFormVisible"
      :close-on-click-modal="false"
    >
      <Editor :content="editorContent" @changeEditor="getEditor"></Editor>
      <div slot="footer" class="dialog-footer">
        <el-button
          type="primary"
          style="width: 30%"
          @click="confirmBtn"
          >确 定</el-button
        >
      </div>
    </el-dialog>
  </div>
</template>

<script>
import Editor from "@/components/Editor.vue";
import utils from '@/api/utils'
export default {
  name: "ArgeeManage",
  components: { Editor },
  data() {
    return {
      params: {
        pageNum: 1,
        pageSize: 10,
      },
      // 富文本值
      editorContent: '',
      // htmlForEditor: "<h1>Some initial content</h1>",
      tableData: [], 
      total: 0, //总条数
      tableColumn: [
        { prop: "id", label: "编号" },
        { prop: "title", label: "协议名称", width: 300 },
      ],
      selectData: [],
      dialogFormVisible: false,
      tableId: '',
    };
  },
  created() {
    this.getArgeeList();
  },
  methods: {
    getEditor(html) {
      this.editorContent = html;
      console.log(this.editorContent,111);
      
    },
    // 获取列表
    getArgeeList() {
      this.$Axios.post('/agreement/page',JSON.stringify(this.params),{contentType: 'application/json'}) 
      .then( res=> {
        if(res.data.data) {
          this.tableData = res.data.data.records;
          this.total = res.data.data.total;
        }
      }).catch(err=> console.log(err))
    },

    // 确定修改
    confirmBtn() {
      if(utils.isEmpty(this.editorContent)) {
        this.$message.warning('请填写协议内容')
        return;
      }
      let params = {
        id: this.tableId,
        content: this.editorContent
      }
      this.$Axios.put('/agreement', JSON.stringify(params), {contentType: 'application/json'})
      .then( res=> {
        if( res.data.data) {
          this.$message.success('修改成功');
          this.dialogFormVisible = false;
          this.getArgeeList();
        }
      }).catch( err=> console.log(err))
    },
    
    handleSelectionChange(val, row) {
      this.selectData = val;
    },
    
    // 修改按钮
    editData(row) {
      this.editorContent = '';
      this.tableId = row.id;
      this.$Axios.get('/agreement/'+ row.id).then( res=> {
        if( res.data.data ) {
          this.editorContent = res.data.data.content;
        }
      }).catch(err=>console.log(err))
      this.dialogFormVisible = true;
    },
    // 删除
    delData(row) {
      this.$confirm("确定要删除吗?", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning",
      })
        .then(() => {
          this.$Axios.delete('/agreement/'+ row.id).then( res=> {
            if(res.data.data) {
              this.$message.success('删除成功');
              this.getArgeeList();
            }
          }).catch(err=>console.log(err))
        })
        .catch(() => {
          console.log("已取消");
        });
    },
    handleCurrentChange(page) {
      this.params.pageNum = page;
      this.getArgeeList();
    },
    handleSizeChange(val) {
      this.params.pageSize = val;
      this.getArgeeList();
    },
  },
};
</script>

<style lang="less" scoped>
.argeeManage {
  // height: 100%;
  /deep/ .ql-container {
    min-height: 300px;
  }
  /deep/ .el-dialog__footer {
    text-align: center;
  }
}
</style>