<template>
  <div class="batch-clustering">
    <el-card class="common-table-container">
      <div class="common-header-box">
        <h3>批量加群</h3>
      </div>
      <main>
        <el-form :model="ruleForm" :rules="rules" ref="ruleForm" label-width="120px" class="demo-ruleForm">
          <el-form-item label="群ID：" prop="groupOuterId">
            <el-input v-model="ruleForm.groupOuterId" clearable></el-input>
          </el-form-item>
          <el-form-item label="群管手机：" prop="adminMobile">
            <el-input v-model="ruleForm.adminMobile" clearable></el-input>
          </el-form-item>
          <el-form-item label="成员手机：" prop="mobiles">
            <el-input type="textarea" rows="15" v-model="ruleForm.mobiles" clearable></el-input>
            <p class="tips">* 多个手机号之间以英文逗号分割 ，上限50个</p>
          </el-form-item>
          <el-form-item>
            <el-button type="primary" @click="submitForm('ruleForm')">提交</el-button>
            <el-button @click="resetForm('ruleForm')">重置</el-button>
          </el-form-item>
        </el-form>
      </main>
      <div class="common-table-footer">


      </div>
    </el-card>
  </div>
</template>

<script>
export default {
  name: "BatchClustering",
  data() {
    return {
      ruleForm: {
        groupOuterId: "",
        adminMobile: "",
        mobiles: "",
      },
      rules: {
        groupOuterId: [
          { required: true, message: '请输入群ID', trigger: 'blur' }
        ],
        adminMobile: [
          { required: true, message: '请输入群管手机', trigger: 'blur' }
        ]
      }
    };
  },
  created() {
  },
  methods: {
    // 群添加新成员
    async groupAddMember() {
      let res = await this.$Axios.post("im/group/add-member", JSON.stringify(this.ruleForm), { contentType: 'application/json' });
      // console.log(res);
      let { code } = res.data;
      if (code === "000000") {
        this.$message.success("添加成功！")
      }
    },
    // 表单提交
    submitForm(formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          this.groupAddMember(); //群添加新成员
          this.$refs[formName].resetFields(); //重置表单
        }
      });
    },
    // 重置表单
    resetForm(formName) {
      this.$refs[formName].resetFields();
    }
  },
};
</script>

<style lang="less" scoped>
.batch-clustering {
  main {
    width: 50%;

    .tips {
      font-size: 12px;
      color: rgb(235, 4, 4);
    }
  }
}
</style>