<template>
  <div class="cashOutManage">
    <el-card class="common-search-box">
      <div class="common-search-container">
        <div class="search-item">
          <SearchDatePicker
            title="提现日期"
            :value="params.dateRange"
            @searchDateChange="searchDateChange"
          ></SearchDatePicker>
        </div>
        <div class="search-item">
          <span class="title">提现状态</span>
          <el-select v-model="params.status" placeholder="请选择" clearable>
            <el-option
              v-for="item in cashOutTypeList"
              :key="item.status"
              :label="item.statusName"
              :value="item.status"
            >
            </el-option>
          </el-select>
        </div>
        <div class="search-item">
          <span class="title">搜索名称</span>
          <el-input
            v-model="params.keyword"
            suffix-icon="el-icon-search"
            placeholder="请输入关键词搜索"
            @input="params.keyword = params.keyword.trim()"
          ></el-input>
        </div>
        <div class="search-item search-btn">
          <el-button @click="query('query')" type="primary">查询</el-button>
          <el-button @click="reset">重置</el-button>
        </div>
      </div>
    </el-card>
    <el-card class="common-table-container">
      <div class="common-header-box">
        <h3>提现列表</h3>
      </div>
      <el-table
        ref="multipleTable"
        :data="tableData"
        @select="handleSelectionChange"
        @select-all="handleSelectionChange"
        border
        height="100%"
      >
        <el-table-column type="selection" width="55"></el-table-column>
        <el-table-column
          v-for="item in tableColumn"
          :key="item.label"
          :label="item.label"
          :prop="item.prop"
          :min-width="item.width || 120"
        ></el-table-column>
        <!-- <el-table-column label="操作" width="160px">
          <template slot-scope="scope">
            <el-button v-if="scope.row.status !== 2" @click="updatePay(scope.row,'agree')" type="text"
              >同意打款</el-button
            >
            <el-button v-if="scope.row.status !== 2" @click="updatePay(scope.row,'reject')" type="text">驳回</el-button>
          </template>
        </el-table-column> -->
      </el-table>

      <div class="common-table-footer">
        <div>
          <el-checkbox
            v-model="checkAll"
            @change="checkAllChange"
            style="margin-right: 30px"
            >全选</el-checkbox
          >
          <el-button @click="exportList">导出所选</el-button>
          <el-button @click="exportAllList">导出全部</el-button>
        </div>
        <el-pagination
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
          :current-page="params.pageNum"
          :page-sizes="[10, 20, 30, 40]"
          :page-size="params.pageSize"
          layout="total, sizes, prev, pager, next, jumper"
          :total="total"
        >
        </el-pagination>
      </div>
    </el-card>
  </div>
</template>

<script>
import utils from "@/api/utils";
export default {
  name: "CashOutManage",
  data() {
    return {
      params: {
        dateRange: "",
        keyword: "",
        status: "",
        pageSize: 10,
        pageNum: 1,
      },
      params2: {
        dateRange: "",
        keyword: "",
        status: "",
        pageSize: 10,
        pageNum: 1,
      },
      cashOutTypeList: [],
      tableData: [],
      total: 0, //总条数
      tableColumn: [
        { prop: "id", label: "编号", width: 160},
        { prop: "userNickName", label: "提现用户", width: 170},
        { prop: "moneyAmount", label: "提现金额" },
        { prop: "bankCardNum", label: "银行账号", width: 160},
        { prop: "userMobile", label: "手机号" },
        { prop: "statusText", label: "提现状态" },
        { prop: "updateTime", label: "提现日期", width: 150},
        { prop: "createTime", label: "处理日期", width: 150},
      ],
      // 选中全部数据，默认不选中
      checkAll: false,
      // 选中数据
      selectData: [],
    };
  },
  created() {
    this.queryCashOutType();
  },
  methods: {
    searchDateChange(val) {
      this.params.dateRange = val;
    },
    // 查询提现状态
    queryCashOutType() {
      this.$Axios
        .get("/wallet-withdraw-record/status-list")
        .then((res) => {
          if (res.data.data) {
            this.cashOutTypeList = res.data.data;
            this.query();
          }
        })
        .catch((err) => console.log(err));
    },
    // 查询
    query(type) {
      if (type == "query") {
        this.params.pageNum = 1;
        this.params2 = JSON.parse(JSON.stringify(this.params));
      } else {
        this.params2.pageNum = this.params.pageNum;
        this.params2.pageSize = this.params.pageSize;
      }
      let params = JSON.parse(
        JSON.stringify(utils.objDeleteNull(this.params2))
      );
      params.dateRange = this.params2.dateRange
        ? this.params2.dateRange.join(" ~ ")
        : null;
      this.$Axios
        .post("/wallet-withdraw-record/page", JSON.stringify(params), {
          contentType: "application/json",
        })
        .then((res) => {
          if (res.data.data) {
            res.data.data.records.forEach((item) => {
              item.statusText = utils.arrayToObj(this.cashOutTypeList,'status','statusName')[item.status];
            });
            // console.log(this.cashOutTypeList,666);
            
            this.tableData = res.data.data.records;
            this.total = res.data.data.total;
          }
        })
        .catch((err) => {
          console.log(err);
        });
    },
    // 重置按钮
    reset() {
      this.params.userKeyword = null;
      this.params.dateRange = null;
      this.params.status = null;
      this.params.keyword = "";
      this.query("query");
    },
    // 导出所选
    exportList() {
      if (this.selectData.length > 0) {
        let ids = utils.arrayOpera(this.selectData, "id");
        let params = {
          ids,
          status: this.params2.status,
          keyword: this.params2.keyword,
          dateRange: this.params2.dateRange
            ? this.params2.dateRange.join(" ~ ")
            : null,
        };
        this.$Axios
          .post("/wallet-withdraw-record/export", JSON.stringify(params), {
            responseType: "blob",
            contentType: "application/json",
          })
          .then((data) => {
            if (data) {
              utils.excelDownload(data, "导出提现列表.xls");
            }
          })
          .catch((err) => {
            console.log(err);
          });
      } else {
        this.$message.warning("请选择导出项");
        return;
      }
    },
    exportAllList() {
      let params = {
        userKeyword: this.params2.userKeyword,
        dateRange: this.params2.dateRange
          ? this.params2.dateRange.join(" ~ ")
          : null,
      };
      this.$Axios
        .post("/wallet-withdraw-record/export", JSON.stringify(params), {
          responseType: "blob",
          contentType: "application/json",
        })
        .then((data) => {
          if (data) {
            utils.excelDownload(data, "导出全部提现列表.xls");
          }
        })
        .catch((err) => {
          console.log(err);
        });
    },
    updatePay(row, type) {
      let tips = '打款';
      let status = 2;
      if( type == 'reject') {
        tips = '驳回';
        status = 3;
      }
      this.$confirm(`确定要${tips}吗?`, "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning",
      })
        .then(() => {
          let params = {
            id: row.id,
            status
          }
          this.$Axios.put('/wallet-withdraw-record/status',JSON.stringify(params),{contentType: 'application/json'})
          .then( res=> {
            if( res.data.data) {
              this.$message.success(`${tips}成功`);
              this.query();
            }
          }).catch(err=>console.log(err))
        })
        .catch(() => {
          console.log("已取消");
        });
    },
    handleCurrentChange(page) {
      this.params.pageNum = page;
      this.query();
    },
    handleSizeChange(val) {
      this.params.pageSize = val;
      this.query();
    },
    handleSelectionChange(val) {
      this.selectData = val;
      this.checkAll = val.length == this.tableData.length;
    },
    // 选中全部数据
    checkAllChange(val) {
      console.log(val,11111);
      if (val) {
        this.$refs.multipleTable.toggleAllSelection();
        // this.checkAll = true
      } else {
        this.$refs.multipleTable.clearSelection();
        this.selectData = [];
      }
    },
  },
};
</script>

