import axios from 'axios'
// import store from '@/store'
import router from '@/router/index.js'
import qs from "qs"
import {
    Loading,
    Message
} from 'element-ui'

var cancelTokenCollection = {}
// 创建一个 axios 实例
const instance = axios.create({
    baseURL: process.env.VUE_APP_BASE_URL,
    timeout: 5000, // request timeout
    validateStatus: function (status) {
        return status < 500
    },
    headers: {
        // 跨域请求 这个配置不能少  application/json
        'Content-Type': 'application/x-www-form-urlencoded;charset=UTF-8',
        'Accept': 'application/json'
    }
})

let Load = null



//请求拦截器
instance.interceptors.request.use(config => {

    let accessToken = sessionStorage.accessToken
    if (accessToken) {
        config.headers['Authorization'] = accessToken
    }
    // 调用Loading方法
    if (!Load) {
        Load = Loading.service()
    }
    return config
}, error => {
    if (Load) {
        if (Load.visible) {
            Loading.service().close()
            Load = null
        }
    }
    return Promise.reject(error)

})

instance.interceptors.response.use(response => {

    // 关闭Loading动画
    if (Load) {
        if (Load.visible) {
            Loading.service().close()
            Load = null
        }
    }
    if (response.data instanceof Blob) {
        if (response.headers['content-disposition']) {
            return response;
        } else {
            if (response.headers['content-type'] && response.headers['content-type'].includes('json')) {
                const reader = new FileReader();
                reader.onload = () => {
                    const { result } = reader;
                    const errorInfos = JSON.parse(result);
                    const { message } = errorInfos;
                    Message.error(message) // 提示错误消息
                    return Promise.reject(new Error(message))
                }
                reader.readAsText(response.data);
            } else {
                return response;
            }
        }
    } else {
        let { code, message, error } = response.data;
        if (response && response.status == 200) {
            if (code && code != '000000') {
                Message.error(message) // 提示错误消息
                if (code === '400110') {
                    router.replace('/login');
                    // location.reload();
                }
                return Promise.reject(new Error(message));
            } else {
                return response;
            }
        } else {
            Message.error(error)
            return Promise.reject(new Error(error));
        }
    }

}, error => {
    console.log(error, 'error');
    if (Load) {
        if (Load.visible) {
            Loading.service().close()
            Load = null
        }
    }
    // 对响应错误做点什么
    if (error.message.indexOf('timeout') != -1) {
        Message.error('网络超时');
    } else if (error.message == 'Network Error') {
        Message.error('网络连接错误');
    } else {
        Message.error(error.response.data);
    }
    return Promise.reject(new Error(error))

});

export default {
    /**
     * @param urlKey 对应API 中的urlkey
     * @param paramObj  发送ajax 传递的参数对象
     * @returns {promise} 返回promise对象
     */
    get: function (urlKey, paramObj) {
        let params = {
            params: paramObj
        };
        return instance.get(urlKey, params);
    },
    post: function (urlKey, paramObj, configs) {

        var myConfig = {
            cancelToken: new axios.CancelToken(function (cancel) {
                cancelTokenCollection[urlKey] = cancel;
            })
        };
        //修改Content-Type  参数为qs.stringify(paramObj)改为paramObj
        if (configs && configs.contentType) {
            myConfig.headers = {
                'Content-Type': configs.contentType
            };
        }
        if (configs && configs.responseType) {
            myConfig.responseType = configs.responseType
        }
        return instance.post(urlKey, qs.stringify(paramObj) || paramObj, myConfig);
    },

    /**
     * @param urlKey 对应API 中的url
     * @param paramObj  发送ajax 传递的参数对象
     * @returns {promise} 返回promise对象
     */
    put: function (urlKey, paramObj, configs) {
        var myConfig = {
            cancelToken: new axios.CancelToken(function (cancel) {
                cancelTokenCollection[urlKey] = cancel;
            })
        };
        //修改Content-Type  参数为qs.stringify(paramObj)改为paramObj
        if (configs && configs.contentType) {
            myConfig.headers = {
                'Content-Type': configs.contentType
            };
        }
        return instance.put(urlKey, qs.stringify(paramObj) || paramObj, myConfig);
    },

    /**
     * @param urlKey 对应API 中的url
     * @param paramObj  发送ajax 传递的参数对象
     * @returns {promise} 返回promise对象
     */
    delete: function (urlKey, paramObj) {
        let params = {
            params: paramObj
        };
        return instance.delete(urlKey, params);
    },

    /**
     * @param configObj 发送ajax 的配置参数
     * @returns {promise} 返回promise对象
     */
    axios: function (configObj) {
        return axios(configObj)
    },

    all: function () {
        axios.all(arguments).then(axios.spread(function () {
            console.dir(arguments)
        }))
    },

    // 终止 ajax 请求， 如 xhr 的 abort 方法
    cancel: function (urlKey) {
        if (!cancelTokenCollection[urlKey]) {
            return
        }
        cancelTokenCollection[urlKey]()
    }
};