<template>
  <div class="RoleUpdate">
    <el-card class="common-table-container">
      <div class="common-header-box editInfo-headerBox">
        <h3 v-if="isAdd">添加角色</h3>
        <h3 v-else>编辑角色</h3>
      </div>
      <el-form ref="roleUpdate" :rules="rules" :model="editRole" label-width="80px" class="main-box">
        <el-form-item label="角色名称" prop="appellation">
          <el-input v-model="editRole.appellation" placeholder="请填写名称"></el-input>
        </el-form-item>
        <el-form-item label="角色描述">
          <el-input v-model="editRole.description" placeholder="请填写描述"></el-input>
        </el-form-item>  
      </el-form>
      <div class="common-table-footer">
        <div class="button-box">
            <el-button type="primary" @click="saveRole(isAdd)">保存</el-button>
            <el-button @click="$router.replace({name: 'PermissionManage'})">取消</el-button>
        </div>
      </div>
    </el-card>
  </div>
</template>

<script>
export default {
  name: "RoleUpdate",
  data() {
    return {
      editRole: {
        appellation: '',
        description: '',
      },
      isAdd: true,
      rules: {
        //表单验证规则
        appellation: [
          { required: true, message: "请输入角色名称", trigger: "blur" }
        ]
      },
    };
  },
  created() {
    this.isAdd = this.$route.query.type === 'add' ? this.isAdd : false;
    this.editRole = this.$route.query.data ? JSON.parse(this.$route.query.data) : this.editRole;
    
  },

  methods: {
    saveRole(add) {
      this.$refs.roleUpdate.validate(async (vali) => {
        if(vali) {
          let url = '/role';
          let params  = {...this.editRole};
          let msg = '添加成功'
          params.status = params.status ? 1 : 0;
          if (!add) {
            msg = '编辑成功'
            this.$Axios.put(url,JSON.stringify(params),{contentType: 'application/json'})
            .then( res=> {
              if(res.data.data) {
                this.$message.success(msg);
                this.$router.replace('/permissionManage')
              }
            })
            return
          }
          this.$Axios.post(url,JSON.stringify(params),{contentType: 'application/json'})
          .then( res=> {
            if(res.data.data) {
              this.$message.success(msg);
              this.$router.replace('/permissionManage')
            }
          })
        } else {
          return;
        }
      })
      
    }
  }
};
</script>

<style lang="less" scoped>
.RoleUpdate {
  // height: 100%;
  .main-box {
    .el-input {
        width: 35%;
    }
 
  }
}
</style>