<template>
    <div class="otherInfo">
        <el-card class="common-table-container">
            <div class="common-header-box editInfo-headerBox">
                <h3>其他信息修改</h3>
            </div>
            <div class="main-box">
                <div class="item">
                    <h5 class="title">高级群价格</h5>
                    <el-input v-model="editForm.highLevelGroupSale" type="number" :min="0" placeholder="请填写单价"></el-input>
                </div>
                <div class="item">
                    <h5 class="title">超级群价格</h5>
                    <el-input v-model="editForm.superLevelGroupSale" type="number" min="0" placeholder="请填写单价"></el-input>
                </div>
            </div>
            <div class="common-table-footer">
                <div>
                    <el-button @click="submitInfo" type="primary">保存</el-button>
                    <el-button @click="$router.go(-1)">返回</el-button>
                </div>
            </div>
        </el-card>
    </div>
</template>

<script>
import utils from '@/api/utils'
export default {
    name: "OtherInfo",
    data() {
        return {
            editForm: {
                highLevelGroupSale: '',
                superLevelGroupSale: '',
            },
        }
    },
    created() {
        this.getOtherDetail();
    },
    methods: {
        getOtherDetail() {
            this.$Axios.get('/other').then(res => {
                if (res.data.data) {
                    this.editForm = res.data.data;
                }
            }).catch(err => console.log(err))
        },
        submitInfo() {
            if (utils.isEmpty(this.editForm.highLevelGroupSale)) {
                this.$message.warning('请填写高级群价格')
                return
            }
            if (utils.isEmpty(this.editForm.superLevelGroupSale)) {
                this.$message.warning('请填写超级群价格')
                return
            }
            if (this.editForm.highLevelGroupSale < 0 || this.editForm.superLevelGroupSale < 0) {
                this.$message.warning('请填写正确的价格价格');
                return
            }
            this.$Axios.put('/other', JSON.stringify(this.editForm), { contentType: 'application/json' })
                .then(res => {
                    if (res.data.data) {
                        this.$message.success('保存成功');
                        this.getOtherDetail();
                    }
                }).catch(err => console.log(err))
        },
    },
}
</script>

<style lang="less" scoped>
.otherInfo {
    .main-box {
        .item {
            margin-bottom: 35px;

            .el-input {
                width: 35%;
            }

            .title {
                margin-bottom: 20px;
                color: #606266;
                font-weight: normal;
            }
        }
    }
}
</style>