<template>
  <el-container>
    <el-aside width="12%" style="min-width: 160px">
      <div class="aside-head-box">
        <el-dropdown trigger="click">
          <div class="aside-headinfo-box">
            <img
              :src="accountInfo.headUrl"
              alt=""
            />
            <h3>{{ accountInfo.nickName || 'admin' }}</h3>
            <span class="el-dropdown-link" style="color: #8691a1">
              {{ accountInfo.role == 1 ? '客户' : '客服'}}<i class="el-icon-arrow-down el-icon--right"></i>
            </span>
          </div>
          <el-dropdown-menu slot="dropdown">
            <el-dropdown-item @click.native="$router.push('/editInfo')"
              >修改资料</el-dropdown-item
            >
            <el-dropdown-item @click.native="logOut">退出登录</el-dropdown-item>
          </el-dropdown-menu>
        </el-dropdown>
      </div>
      <el-menu
        :collapse="false"
        :default-active="active"
        background-color="#2E4050"
        text-color="#8691A1"
        active-text-color="#fff"
        unique-opened
        router
      >
        <!-- 循环div, 根据数据决定渲染submenu还是menu-item -->
        <div v-for="item in roleList" :key="item.index">
          <!-- 可折叠的菜单 -->
          <el-submenu v-if="item.children" :index="item.index">
            <!-- 控制折叠菜单的名字和图标 -->
            <template slot="title">
              <i :class="item.icon"></i>
              <span>{{ item.appellation }}</span>
            </template>
            <el-menu-item
              v-for="child in item.children"
              :key="child.linkUrl"
              :index="child.linkUrl"
              >{{ child.appellation }}</el-menu-item
            >
          </el-submenu>
          <!-- 不可折叠的菜单 -->
          <el-menu-item v-else :index="item.linkUrl">
            <i :class="item.icon"></i>
            <span slot="title">{{ item.appellation }}</span>
          </el-menu-item>
          <!-- <el-submenu index="1-4">
          <template slot="title">选项4</template>
          <el-menu-item index="1-4-1">选项1</el-menu-item>
        </el-submenu> -->
        </div>
      </el-menu>
    </el-aside>
    <el-container>
      <el-header>
        <el-breadcrumb separator="/">
          <el-breadcrumb-item :to="{ path: '/home' }">主页</el-breadcrumb-item>
          <el-breadcrumb-item  v-for="(item, i) in breadArr" :key="i">{{
            item.title
          }}</el-breadcrumb-item>
        </el-breadcrumb>
        <div class="header-rightBox">
          <el-button @click="logOut">退出登录</el-button>
        </div>
      </el-header>
      <el-main>
        <!-- <el-card> -->
        <transition name="slide-left" mode="out-in">
          <router-view></router-view>
        </transition>
        <!-- </el-card> -->
      </el-main>
    </el-container>
  </el-container>
</template>

<script>

export default {
  data() {
    return {
      isCollapse: false,
      breadArr: [],
      active: "/homeMain", //树选项卡选中的
      // treeList: []
      //   {
      //     index: "/homeMain",
      //     icon: "iconfont el-icon-monitor",
      //     appellation: "主页",
      //     children: [{ linkUrl: "/homeMain", appellation: "控制台" }],
      //   },
      //   {
      //     index: "userManage",
      //     icon: "iconfont el-icon-user",
      //     appellation: "用户管理",
      //     children: [
      //       { linkUrl: "/userList", appellation: "用户列表" },
      //       { linkUrl: "/groupList", appellation: "群列表" },
      //     ],
      //   },
      //   {
      //     index: "financeManage",
      //     icon: "iconfont el-icon-help",
      //     appellation: "财务管理",
      //     children: [
      //       { linkUrl: "/transactionRecord", appellation: "交易记录" },
      //       { linkUrl: "/cashOutManage", appellation: "提现管理" },
      //     ],
      //   },
      //   {
      //     index: "customerServiceManage",
      //     icon: "iconfont el-icon-picture-outline-round",
      //     appellation: "客服管理",
      //     children: [
      //       { linkUrl: "/userReport", appellation: "用户举报" },
      //       { linkUrl: "/userConsult", appellation: "用户咨询" },
      //     ],
      //   },
      //   {
      //     index: "infoUpholdManage",
      //     icon: "iconfont el-icon-c-scale-to-original",
      //     appellation: "信息维护",
      //     children: [
      //       { linkUrl: "/argeeManage", appellation: "协议管理" },
      //       { linkUrl: "/commonProblem", appellation: "常见问题" },
      //       { linkUrl: "/notice", appellation: "通知公告" },
      //       { linkUrl: "/otherInfo", appellation: "其他信息" },
      //     ],
      //   },
      //   {
      //     index: "systemSettings",
      //     icon: "iconfont el-icon-setting",
      //     appellation: "系统设置",
      //     children: [
      //       { linkUrl: "/permissionManage", appellation: "权限管理" },
      //       { linkUrl: "/accountManage", appellation: "账号管理" },
      //       { linkUrl: "/systemLog", appellation: "系统日志" },
      //       { linkUrl: "/editInfo", appellation: "修改资料" },
      //     ],
      //   },

        // {
        //   index: "inquiry",
        //   icon: "iconfont el-icon-search",
        //   appellation: "IM查询",
        //   children: [
        //     { linkUrl: "/groupReport", appellation: "群报表" },
        //     { linkUrl: "/userList", appellation: "用户列表" },
        //     { linkUrl: "/accountChangeInfo", appellation: "账变信息" },
        //     { linkUrl: "/personalRedPacketList", appellation: "个人红包列表" },
        //     { linkUrl: "/groupRedPacketList", appellation: "群红包列表" },
        //     { linkUrl: "/messageCode", appellation: "短信验证码" },
        //     { linkUrl: "/autoRegisterUser", appellation: "自动注册用户" },
        //     { linkUrl: "/systemAccount", appellation: "系统切账" },
        //   ],
        // },
        // {
        //   index: "manage",
        //   icon: "iconfont el-icon-s-management",
        //   appellation: "IM查询",
        //   children: [
        //     { linkUrl: "/batchEditPwd", appellation: "批量改密码" },
        //     { linkUrl: "/batchAddFriend", appellation: "批量加好友" },
        //     { linkUrl: "/batchClustering", appellation: "批量加群" }
        //   ],
        // },

      // ],
    };
  },
  created() {
    // 根据当前hash地址,设置左侧默认选中的选项卡
    if (this.$route.meta.parent && this.$route.meta.parent.length > 1) {
      this.active = this.$route.path.slice(
        0,
        this.$route.path.lastIndexOf("/")
      );
    } else {
      this.active = this.$route.path;
    }

    this.filterBread();    
  },
  methods: {
    //计算面包屑导航函数
    filterBread() {
      let arr = [];
      this.$route.matched.forEach((item) => {
        if (item.path !== "" && item.meta.title) {
          if (item.meta.parent) {
            item.meta.parent.forEach((data) => {
              arr.push({
                title: data.title,
              });
            });
            arr.push({
              title: item.meta.title,
            });
          } else {
            arr.push({
              title: item.meta.title,
            });
          }
        }
      });
      // 把新数组重新赋值给定义的遍历
      this.breadArr = arr;
    },
    // 退出登录
    logOut() {
      this.$confirm("您确定要退出登录吗?", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning",
      })
        .then(() => {
          this.$store.commit('loginOut');
          sessionStorage.clear();
          this.$router.replace("/login");
          // location.reload();
        })
        .catch(() => {
          console.log("已取消");
        });
    },
   
  },
  computed: {
    accountInfo() {
      return sessionStorage.accountInfo ? JSON.parse(sessionStorage.accountInfo) : {};
    },
    //根据用户权限过滤权限数组
    roleList() {
      return sessionStorage.roleMenu ? JSON.parse(sessionStorage.roleMenu) : [];
    }
  },
  //侦听器
  watch: {
    "$route.path"() {
      //重新计算面包屑
      this.filterBread();
      if (this.$route.meta.parent && this.$route.meta.parent.length > 1) {
        this.active = this.$route.path.slice(
          0,
          this.$route.path.lastIndexOf("/")
        );
      } else {
        this.active = this.$route.path;
      }
    },
  },
};
</script>

<style lang="less" scoped>
@basefont: #333;
@baseAside: #2e4050;
@hoverColor: #fff;
.el-container {
  width: 100%;
  height: 100%;
}
.el-header {
  background-color: #f3f3f4;
  border-bottom: 1px solid #e4e4e4;
  color: @basefont;
  display: flex;
  justify-content: space-between;
  align-items: center;

  div {
    display: flex;
    align-items: center;
    img {
      width: 50px;
      height: 50px;
      border-radius: 50%;
    }
  }
  .header-rightBox {
    .badge-item {
      margin-right: 20px;
      font-size: 20px;
    }
  }
}

.el-aside {
  background-color: @baseAside;
  .aside-head-box {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 30px 0 20px 0;
    text-align: center;
    .aside-headinfo-box {
      cursor: pointer;
      img {
        // cursor: pointer;
        width: 80px;
        height: 80px;
        border-radius: 50%;
      }
      h3 {
        color: @hoverColor;
        margin: 10px 0;
        font-weight: normal;
      }
    }
  }
}
.iconfont {
  font-size: 16px;
  margin-right: 15px;
}

.el-main {
  background-color: #f2f2f2;
  padding: 30px 60px;
}
.el-dropdown-link:hover {
  color: @hoverColor !important;
}
.el-menu {
  border: 0;
}
/deep/ .el-menu-item {
  background: #293746 !important;
  padding-left: 50px !important;
  &:hover {
    color: @hoverColor !important;
  }
}
/deep/ .el-submenu__title:hover {
  background: @baseAside !important;
  color: @hoverColor !important;
  & i {
    color: @hoverColor;
  }
}
/deep/ .el-submenu.is-active .el-submenu__title {
  color: @hoverColor !important;
  i {
    color: @hoverColor;
  }
}

.slide-left-enter {
  opacity: 0;
  transform: translateX(30px);
}
.slide-left-enter-active {
  transition: all 0.3s ease;
}
.slide-left-leave-to {
  opacity: 0;
  transform: translateX(-30px);
}
.slide-left-leave-active {
  transition: all 0.3s ease;
}

</style>