<template>
  <div class="consultDetail">
    <el-card class="common-table-container">
      <div class="common-header-box editInfo-headerBox">
        <div class="common-header-box">
          <h3>咨询详情</h3>
        </div>
      </div>
      <ul v-if="detailList.length > 0" class="detailList">
        <li v-for="item in detailList" :key="`${item.consultId}-${item.createTime}`">
          <div class="detailList-headerBox">
            <img :src="item.headUrl" alt="" />
            <div>
              <span>{{ item.userNickName }}</span>
              <p>{{ item.createTime }}</p>
            </div>
          </div>
          <div class="detailList-content">
            <p v-if="item.content">{{ item.content }}</p>
            <div class="imgContentBox" v-if="item.pictureUrl && item.pictureUrl.length > 0">
              <img v-imgview v-for="(item, index) in item.pictureUrl" :key="index" :src="item" alt="">
            </div>
          </div>
        </li>
      </ul>
      <div v-else style="font-size:14px;margin:30px;">暂无内容</div>
      <div class="common-table-footer">
        <div>
          <el-button type="primary" v-if="adminStatus != 2" @click="answerForm = {}; dialogVisible = true">回复</el-button>
          <el-popconfirm v-if="adminStatus != 2" confirm-button-text='确认' cancel-button-text='取消' icon="el-icon-info" icon-color="red" @confirm="closeHandle"
            title="确认关闭？关闭后将无法恢复！">
            <el-button slot="reference" type="primary" style="margin:0 10px;">关闭</el-button>
          </el-popconfirm>
          <!-- <el-button slot="reference" v-if="status != 2">删除</el-button> -->
          <el-button @click="$router.go(-1)">返回</el-button>
        </div>
      </div>
    </el-card>
    <el-dialog title="回复" :visible.sync="dialogVisible" width="40%">
      <el-form :model="answerForm">
        <el-form-item>
          <el-input type="textarea" v-model="answerForm.content" placeholder="请填写描述" :rows="7"></el-input>
        </el-form-item>
        <el-form-item>
          <UploadHead @imageUrl="getImageUrl" :echoImgUrl="answerForm.pictureUrl"></UploadHead>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button style="width: 30%;" type="primary" @click="submitAnswer">确 定</el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
import UploadHead from "@/components/UploadHead.vue"
import utils from "@/api/utils"
export default {
  name: "ConsultDetail",
  components: {
    UploadHead
  },
  data() {
    return {
      dialogVisible: false,
      answerForm: {
        content: '',
        pictureUrl: '',
      },
      detailList: [],
      adminStatus: this.$route.query.adminStatus
    };
  },
  created() {
    this.getConsultList();
  },
  methods: {
    // 获取回传图片
    getImageUrl(val) {
      this.answerForm.pictureUrl = val.url;
    },
    getConsultList() {
      let id = this.$route.query.id;
      this.$Axios.get('/customer-service/user-consult/detail-list', { id })
        .then(res => {
          if (res.data.data) {
            this.detailList = res.data.data;
            this.detailList.forEach(item => {
              item.pictureUrl = item.pictureUrl ? item.pictureUrl.split(';') : null
            });
          }
        }).catch(err => console.log(err))
    },
    // 关闭咨询
    async closeHandle() {
      let id = this.$route.query.id;
      let { data } = await this.$Axios.put(`/customer-service/user-consult/close/${id}`);
      console.log("data,data", data);
      if (data.code === "000000") {
        this.$message.success("关闭成功！");
        this.$router.push({ name: "UserConsult" })
      } else {
        this.$message.error(data.message);
      }
    },
    submitAnswer() {
      if (utils.isEmpty(this.answerForm.content) && utils.isEmpty(this.answerForm.pictureUrl)) {
        this.$message.warning('请填写回复内容')
        return;
      }
      let params = { ...this.answerForm };
      params.consultId = this.$route.query.id;
      if (this.answerForm.content && utils.isEmpty(this.answerForm.pictureUrl)) {
        params.type = 0
      } else if (utils.isEmpty(this.answerForm.content) && this.answerForm.pictureUrl) {
        params.type = 1
      } else {
        params.type = 2
      }
      this.$Axios.post('/customer-service/user-consult/reply', JSON.stringify(params), { contentType: 'application/json' })
        .then(res => {
          if (res.data.data) {
            this.$message.success('回复成功');
            this.getConsultList();
            this.dialogVisible = false;
          }
        }).catch(err => console.log(err))
    }
  },
};
</script>

<style lang="less" scoped>
.consultDetail {

  // height: 100%;
  .detailList {
    li {
      padding: 20px 0;
      border-bottom: 1px solid #f2f2f2;
    }

    .detailList-headerBox {
      display: flex;
      margin-bottom: 20px;
      align-items: center;

      img {
        width: 50px;
        height: 50px;
        border-radius: 50%;
        margin-right: 15px;
      }

      >div {
        p {
          margin-top: 10px;
          font-size: 12px;
          color: #AAA;
        }
      }
    }
  }

  .imgContentBox {
    img {
      margin-top: 10px;
      margin-right: 10px;
      width: 150px;
      height: 150px;

    }
  }

  /deep/ .el-dialog__footer {
    text-align: center;
  }
}
</style>