<template>
  <div>
    <span class="title">{{ title }}</span>
    <el-date-picker
      v-model="searchDate"
      type="daterange"
      range-separator="至"
      start-placeholder="开始日期"
      end-placeholder="结束日期"
      value-format="yyyy-MM-dd"
      :picker-options="pickerOptions"
      @change="searchDateChange($event)"
    ></el-date-picker>
  </div>
</template>

<script>
export default {
  data() {
    return {
      searchDate: '',
    };
  },
  props: ["value","title"],
  created() {
    let vm = this;
    vm.searchDate = vm.value;
  },
  methods: {
    searchDateChange(val) {
      var vm = this;
      vm.$emit('searchDateChange', val)
    },
  },
  watch: {
    value: function () {
      let vm = this;
      vm.searchDate = vm.value;
    },
  },
  computed: {
    pickerOptions() {
      return {
        disabledDate(time) {
          return time.getTime() > Date.now();
        },
      };
    },
  }
};
</script>

