<template>
  <div class="permissionManage">
    <el-card class="common-table-container">
      <div class="common-header-box">
        <h3>权限列表</h3>
        <el-button
          type="primary"
          @click="$router.push({ name: 'RoleUpdate', query: { type: 'add' } })"
          >添加角色</el-button
        >
      </div>
      <el-table ref="multipleTable" :data="tableData" border height="100%">
        <el-table-column type="selection" width="55"></el-table-column>
        <el-table-column
          v-for="item in tableColumn"
          :key="item.label"
          :label="item.label"
          :prop="item.prop"
          :min-width="item.width || 160"
        >
          <template slot-scope="scope">
            <el-switch v-if="item.prop == 'status'" v-model="scope.row.status" @change="editStatus(scope.row)"></el-switch>
            <div v-else>{{scope.row[item.prop]}}</div>
          </template>
        </el-table-column>  
        <el-table-column label="操作" width="250px">
          <template slot-scope="scope">
            <el-button type="text" @click="delRole(scope.row)">删除</el-button>
            <el-button type="text" @click="assignPermission(scope.row)"
              >分配权限</el-button
            >
            <el-button
              type="text"
              @click="
                $router.push({
                  name: 'RoleUpdate',
                  query: { type: 'edit', data: JSON.stringify(scope.row) },
                })
              "
              >编辑</el-button
            >
          </template>
        </el-table-column>
      </el-table>
      <div class="common-table-footer">
        <div></div>
        <el-pagination
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
          :current-page="params.pageNum"
          :page-sizes="[10, 20, 30, 40]"
          :page-size="params.pageSize"
          layout="total, sizes, prev, pager, next, jumper"
          :total="total"
        >
        </el-pagination>
      </div>
    </el-card>
  </div>
</template>

<script>
export default {
  name: "PermissionManage",
  data() {
    return {
      params: {
        pageNum: 1,
        pageSize: 10,
      },
      total: 0, //总条数
      tableColumn: [
        { prop: "id", label: "ID", },
        { prop: "appellation", label: "角色名称", width: 300 },
        { prop: "description", label: "角色描述", width: 300 },
        { prop: "status", label: "启用", width: 80 },
      ],
      tableData: [],
    };
  },
  created() {
    this.getPermissionList(); //初始化权限列表
  },
  methods: {
    // 获取列表
    getPermissionList() {
      this.$Axios.post('/role/page',JSON.stringify(this.params),{contentType: 'application/json'}) 
      .then( res=> {
        if(res.data.data) {
          this.tableData = res.data.data.records;
          this.tableData.forEach(item => {
            item.status = item.status == 1
          });
          this.total = res.data.data.total;
        }
      }).catch(err=> console.log(err))
    },
    assignPermission(item) {
      this.$router.push({ name: "AssignPermission" ,query: {id: item.id}});
    },
    // 启用
    editStatus(row) {
      let params = {...row};
      params.status = params.status ? 1 : 0;
      this.$Axios.put('/role',JSON.stringify(params), {contentType: 'application/json'})
      .then( res=> {
        if( res.data.data ) {
          this.$message.success('操作成功')
          this.getPermissionList();
        }  
      }).catch( err=> console.log(err))
    },
    // 删除
    delRole(row) {
      this.$confirm("确定要删除吗?", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning",
      })
        .then(() => {
          this.$Axios.delete('/role/'+ row.id)
          .then( res=> {
            if( res.data.data ) {
              this.$message.success('删除成功');
              this.getPermissionList();
            }
          }).catch( err=> console.log(err))
        })
        .catch(() => {
          console.log('已取消');
        });
    },
    handleCurrentChange(page) {
      this.params.pageNum = page;
      this.getPermissionList();
    },
    handleSizeChange(val) {
      this.params.pageSize = val;
      this.getPermissionList();
    },
  },
};
</script>

<style lang="less" scoped>
.permissionManage {
  // height: 100%;
}
</style>