// 点击相同路由报警告的解决办法
const originalPush = VueRouter.prototype.push;
VueRouter.prototype.push = function push(location) {
  return originalPush.call(this, location).catch(err => err)
}

let hasMenus = false;
// 路由前置守卫
router.beforeEach((to, from, next) => {
  switch (to.name) {
    case 'RoleUpdate':
      if (to.query.type == 'add') {
        to.meta.title = '添加角色'
      } else {
        to.meta.title = '编辑角色'
      }
      break;
    case 'AccountUpdate':
      if (to.query.type == 'add') {
        to.meta.title = '添加账号'
      } else {
        to.meta.title = '编辑账号'
      }
      break;
  }
  if (to.path === '/login') {
    sessionStorage.clear();
    store.commit('loginOut');
    return next();
  } else {
    const tokenStr = sessionStorage.accessToken; //会话存储中获取token
    if (!tokenStr) {
      // location.reload();
      return next('/login');
    }
    if (hasMenus) {
      next()
    } else {

      const requireContext = require.context('@/views/',true,/\.vue$/)
      const components = {};
      requireContext.keys().map(c=>{
        let ctor = requireContext(c).default
        components[ctor.name] = ctor
      })

      let menus = sessionStorage.roleMenu ? JSON.parse(sessionStorage.roleMenu) : []; //角色下的菜单
      let addMenus = [];
      if (menus && menus.length > 0) { //如果后台传过来路由数组
        menus.forEach(item => {
          if (item.children && item.children.length > 0) { //当前路由下有子路由
            item.children.forEach(item2 => {
              let handleName = item2.linkUrl.slice(1);
              let componentName =  handleName[0].toUpperCase() + handleName.slice(1);
              addMenus.push({
                name: handleName[0].toUpperCase() + handleName.slice(1),
                path: item2.linkUrl,
                meta: {
                  title: item2.appellation,
                  parent: item.appellation == '主页' ? null : [{ title: item.appellation }]
                },
                component: components[componentName]
              });
            });
          } else {
            let handleName = item.linkUrl.slice(1);
            let componentName =  handleName[0].toUpperCase() + handleName.slice(1)
            addMenus.push({
              name: handleName[0].toUpperCase() + handleName.slice(1),
              path: item.linkUrl,
              meta: {
                title: item.appellation,
              },
              component: components[componentName]
            });
          }
        });
      }
      addMenus.map(route => {
        router.addRoute("Home", route); 
      });
      // 通配404
      router.addRoute({
        path: '*',
        component: ()=> import('@/views/Error404.vue')
      })
      hasMenus = true;
      next({...to,replace:true})
    }
  }
})

export default router