<template>
  <div class="notice">
    <el-card class="common-table-container">
      <div class="common-header-box">
        <h3>通知公告列表</h3>
        <el-button @click="releaseNotice" type="primary">发布</el-button>
      </div>
      <el-table
        ref="multipleTable"
        :data="tableData"
        border
        height="100%"
      >
        <el-table-column type="selection" width="55"></el-table-column>
        <el-table-column
          v-for="item in tableColumn"
          :key="item.label"
          :label="item.label"
          :prop="item.prop"
          :min-width="item.width || 160"
        ></el-table-column>
        <el-table-column label="操作" width="200px">
          <template slot-scope="scope">
            <el-button type="text" @click="delData(scope.row)">删除</el-button>
          </template>
        </el-table-column>
      </el-table>
      <div class="common-table-footer">
        <div></div>
        <el-pagination
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
          :current-page="params.pageNum"
          :page-sizes="[10, 20, 30, 40]"
          :page-size="params.pageSize"
          layout="total, sizes, prev, pager, next, jumper"
          :total="total"
        >
        </el-pagination>
      </div>
    </el-card>
    <el-dialog title="发布公告" :visible.sync="dialogVisible" width="25%">
      <el-input
        type="textarea"
        :rows="7"
        placeholder="请填写公告内容"
        v-model="noticeForm.content"
      >
      </el-input>
      <span slot="footer" class="dialog-footer">
        <el-button @click="submitNotice" type="primary" style="width: 100%; margin-top: 20px">确定</el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
import utils from '@/api/utils'
export default {
  name: "Notice",
  data() {
    return {
      params: {
        pageNum: 1,
        pageSize: 10,
      },
      tableData: [], 
      total: 0, //总条数
      tableColumn: [
        { prop: "id", label: "编号", },
        { prop: "content", label: "内容", width: 300 },
      ],
      dialogVisible: false,
      noticeForm: {
        content: '',
      }
    };
  },
  created() {
    this.getNotice();
  },
  methods: {
    getNotice() {
      this.$Axios.post('/announcement/page',JSON.stringify(this.params),{contentType: 'application/json'})
      .then( res=> {
        if( res.data.data ) {
          this.tableData = res.data.data.records;
          this.total = res.data.data.total;
        } 
      }).catch( err=>console.log(err))
    },
    // 发布公告
    releaseNotice() {
      this.noticeForm = {}
      this.dialogVisible = true;
    },
    handleCurrentChange(page) {
      this.params.pageNum = page;
      this.getNotice();
    },
    handleSizeChange(val) {
      this.params.pageSize = val;
      this.getNotice();
    },
    // 发布
    submitNotice() {
      if(utils.isEmpty(this.noticeForm.content)) {
        this.$message.warning('请填写公告内容')
        return;
      }
      this.$Axios.post('/announcement/add',JSON.stringify(this.noticeForm),{contentType: 'application/json'})
      .then( res=> {
        if( res.data.data ) {
          this.$message.success('发布成功');
          this.getNotice();
          this.dialogVisible = false;
        }
      }).catch( err=>console.log(err))
    },
    // 删除
    delData(row) {
      this.$confirm("确定要删除吗?", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning",
      })
        .then(() => {
          this.$Axios.delete('/announcement/' + row.id)
          .then( res=> {
            if( res.data.data ) {
              this.$message.success("删除成功");
              this.getNotice();
            }
          }).catch( err=>console.log(err))
        })
        .catch(() => {
          console.log("已取消");
        });
    },
  },
};
</script>

<style lang="less" scoped>
.notice {
  // height: 100%;
}
</style>