<template>
  <div class="sys-acc">
    <el-card class="common-search-box">
      <h2 class="title">平台切账</h2>
      <div class="common-search-container">
        <div class="search">
          <el-date-picker v-model="createTime" type="datetime" placeholder="选择日期时间" default-time="12:00:00"
            format="yyyy-MM-dd HH:mm" value-format="yyyy-MM-dd HH:mm">
          </el-date-picker>
          <el-button type="primary" icon="el-icon-search" @click="getData"></el-button>
          <el-button type="primary" @click="createAccountInfo">生成切账</el-button>
        </div>
        <div class="tables">
          <div class="table-item">
            <h3>统计信息</h3>
            <el-table :data="accountTableData" border style="width: 100%" height="100%">
              <el-table-column v-for="item in countTableColumn" :key="item.label" :label="item.label" :prop="item.prop"
                :min-width="item.width || 120">
              </el-table-column>
            </el-table>
          </div>
          <AccountTableItem titleText="系统未领个包" :tableParams="sysPersonalParams" :tableColumn="sysPersonalTableColumn"
            @sizeChange="sizeChange('sysPersonal', $event)" @currentChange="currentChange('sysPersonal', $event)">
          </AccountTableItem>

          <AccountTableItem titleText="用户未领个包" :tableParams="userPersonalParams" :tableColumn="userPersonalTableColumn"
            @sizeChange="sizeChange('userPersonal', $event)" @currentChange="currentChange('userPersonal', $event)">
          </AccountTableItem>
          <AccountTableItem titleText="系统未领群包" :tableParams="sysGroupParams" :tableColumn="sysGroupTableColumn"
            @sizeChange="sizeChange('sysGroup', $event)" @currentChange="currentChange('sysGroup', $event)">
          </AccountTableItem>
          <AccountTableItem titleText="个人未领群包" :tableParams="userGroupParams" :tableColumn="userGroupTableColumn"
            @sizeChange="sizeChange('userGroup', $event)" @currentChange="currentChange('userGroup', $event)">
          </AccountTableItem>
        </div>
      </div>
    </el-card>
  </div>
</template>

<script>
import AccountTableItem from "@/components/AccountTableItem.vue";
export default {
  name: "SystemAccount",
  components: { AccountTableItem },
  data() {
    return {
      accountTableData: [], //统计信息列表数据
      sysAccountId: null, //切账id
      createTime: null, //选择日期时间
      // 系统未领个包数据
      sysPersonalParams: {
        tableData: [],
        pageNum: 1,
        pageSize: 10,
        total: 0
      },
      // 用户未领个包数据
      userPersonalParams: {
        tableData: [],
        pageNum: 1,
        pageSize: 10,
        total: 0
      },
      // 系统未领群包数据
      sysGroupParams: {
        tableData: [],
        pageNum: 1,
        pageSize: 10,
        total: 0
      },
      // 用户未领群包数据
      userGroupParams: {
        tableData: [],
        pageNum: 1,
        pageSize: 10,
        total: 0
      },
      // 重置后的数据
      resetData: {
        tableData: [],
        pageNum: 1,
        pageSize: 10,
        total: 0
      },
      //统计信息列表表头
      countTableColumn: [
        { prop: "sysBalance", label: "系统余额" },
        { prop: "userBalance", label: "用户余额" },
        { prop: "sysUnclaimedPersonAmount", label: "系统未领个包" },
        { prop: "userUnclaimedPersonAmount", label: "用户未领个包" },
        { prop: "sysUnclaimedGroupAmount", label: "系统未领群包" },
        { prop: "userUnclaimedGroupAmount", label: "用户未领群包" }
      ],
      //系统未领个包列表表头
      sysPersonalTableColumn: [
        { prop: "fromUserUnionId", label: "发送人ID" },
        { prop: "fromUserName", label: "发送人" },
        { prop: "toUserUnionId", label: "接收人ID" },
        { prop: "toUserName", label: "接收人" },
        { prop: "totalAmount", label: "总金额" }
      ],
      //用户未领个包列表表头
      userPersonalTableColumn: [
        { prop: "fromUserUnionId", label: "发送人ID" },
        { prop: "fromUserName", label: "发送人" },
        { prop: "toUserUnionId", label: "接收人ID" },
        { prop: "toUserName", label: "接收人" },
        { prop: "totalAmount", label: "总金额" }
      ],
      //系统未领群包列表表头
      sysGroupTableColumn: [
        { prop: "groupId", label: "群ID" },
        { prop: "groupName", label: "群名称" },
        { prop: "totalAmount", label: "总金额" }
      ],
      //个人未领群包列表表头
      userGroupTableColumn: [
        { prop: "groupId", label: "群ID" },
        { prop: "groupName", label: "群名称" },
        { prop: "totalAmount", label: "总金额" }
      ]
    };
  },
  created() {
    this.getAccountInfo(); //初始化条件查询统计详情数据
  },
  methods: {
    // 表单搜索查询
    getData() {
      this.getAccountInfo(); //根据时间查询符合条件统计信息
    },
    // 分页查询未领个包  type:  1：系统用户；2：普通用户
    async pagePersonal(type, isQuery = true) {
      if (isQuery) {
        if (type === 1) {
          let res = await this.$Axios.post("cut/account/detail-person/page", JSON.stringify({
            pageSize: this.sysPersonalParams.pageSize,
            pageNum: this.sysPersonalParams.pageNum,
            cutAccountId: this.sysAccountId,
            userType: type
          }), { contentType: 'application/json' });
          if (res.data.code === "000000") {
            this.sysPersonalParams.total = res.data.data.total;
            this.sysPersonalParams.tableData = res.data.data.records;
          }
        } else {
          let res1 = await this.$Axios.post("cut/account/detail-person/page", JSON.stringify({
            pageSize: this.userPersonalParams.pageSize,
            pageNum: this.userPersonalParams.pageNum,
            cutAccountId: this.sysAccountId,
            userType: type
          }), { contentType: 'application/json' })
          if (res1.data.code === "000000") {
            this.userPersonalParams.total = res1.data.data.total;
            this.userPersonalParams.tableData = res1.data.data.records;
          }
        }
      } else {
        this.sysPersonalParams = { ...this.resetData };
        this.userPersonalParams = { ...this.resetData };
        this.sysGroupParams = { ...this.resetData };
        this.userGroupParams = { ...this.resetData };
      }
    },
    // 分页查询未领群包   type:  1：系统用户；2：普通用户
    async pageGroup(type, isQuery = true) {
      if (isQuery) {
        if (type === 1) {
          let res = await this.$Axios.post("cut/account/detail-group/page", JSON.stringify({
            pageSize: this.sysGroupParams.pageSize,
            pageNum: this.sysGroupParams.pageNum,
            cutAccountId: this.sysAccountId,
            userType: type
          }), { contentType: 'application/json' });
          if (res.data.code === "000000") {
            this.sysGroupParams.total = res.data.data.total;
            this.sysGroupParams.tableData = res.data.data.records;
          }
        } else {
          let res1 = await this.$Axios.post("cut/account/detail-group/page", JSON.stringify({
            pageSize: this.userGroupParams.pageSize,
            pageNum: this.userGroupParams.pageNum,
            cutAccountId: this.sysAccountId,
            userType: type
          }), { contentType: 'application/json' })
          if (res1.data.code === "000000") {
            this.userGroupParams.total = res1.data.data.total;
            this.userGroupParams.tableData = res1.data.data.records;
          }
        }
      } else {
        this.sysPersonalParams = { ...this.resetData };
        this.userPersonalParams = { ...this.resetData };
        this.sysGroupParams = { ...this.resetData };
        this.userGroupParams = { ...this.resetData };
      }
    },
    // 条件查询统计详情
    async getAccountInfo() {
      console.log("this-time",this.createTime);
      this.accountTableData = [];
      let res = await this.$Axios.post("cut/account/condition", JSON.stringify({ createTime: this.createTime ? this.createTime : null }), { contentType: 'application/json' });
      let { code, data } = res.data;
      if (code === '000000') {
        this.accountTableData.push(data);
        this.accountTableData = data ? this.accountTableData : [];
        this.sysAccountId = data ? data.id : "";
        console.log(this.sysAccountId);
        if (data) {
          this.pagePersonal(1) //系统未领个包
          this.pagePersonal(2) //用户未领个包
          this.pageGroup(1) //系统未领群包
          this.pageGroup(2) //用户未领群包
        } else {
          this.pagePersonal(1, false) //系统未领个包
          this.pagePersonal(2, false) //用户未领个包
          this.pageGroup(1, false) //系统未领群包
          this.pageGroup(2, false) //用户未领群包
        }

      }
    },
    // 生成切账数据
    async createAccountInfo() {
      this.accountTableData = [];
      let res = await this.$Axios.post("cut/account");
      let { code, data } = res.data;
      if (code === '000000') {
        this.accountTableData.push(data);
        this.accountTableData = data ? this.accountTableData : [];
        this.sysAccountId = data ? data.id : "";
        this.pagePersonal(1) //系统未领个包
        this.pagePersonal(2) //用户未领个包
        this.pageGroup(1) //系统未领群包
        this.pageGroup(2) //用户未领群包
      }
    },
    // 当前页改变
    currentChange(target, val) {
      if (target == "sysPersonal") {
        console.log(target, val);
        this.sysPersonalParams.pageNum = val;  //系统未领个包
        this.pagePersonal(1) //系统未领个包
      } else if (target == "userPersonal") {
        console.log(target, val);
        this.userPersonalParams.pageNum = val;
        this.pagePersonal(2) //用户未领个包
      } else if (target == "sysGroup") {
        console.log(target, val);
        this.sysGroupParams.pageNum = val;
        this.pageGroup(1) //系统未领群包
      } else {
        console.log(target, val);
        this.userGroupParams.pageNum = val;
        this.pageGroup(2) //用户未领群包
      }
    },
    // 每页显示条数改变
    sizeChange(target, val) {
      if (target == "sysPersonal") {
        console.log(target, val);
        this.sysPersonalParams.pageSize = val;
        this.pagePersonal(1) //系统未领个包
      } else if (target == "userPersonal") {
        console.log(target, val);
        this.userPersonalParams.pageSize = val;
        this.pagePersonal(2) //用户未领个包
      } else if (target == "sysGroup") {
        console.log(target, val);
        this.sysGroupParams.pageSize = val;
        this.pageGroup(1) //系统未领群包
      } else {
        console.log(target, val);
        this.userGroupParams.pageSize = val;
        this.pageGroup(2) //用户未领群包
      }
    }
  },
  computed: {},
};
</script>

<style lang="less" scoped>
.sys-acc {

  // height: 100%;
  .title {
    padding-bottom: 10px;
    margin-bottom: 20px;
    border-bottom: 1px solid #ccc;
    color: rgb(129, 129, 129);
    font-size: 16px;
    font-weight: normal;
  }

  .date-picker {
    display: inline-block;
    margin-right: 10px;
  }

  .common-search-container {
    .search {
      margin-bottom: 20px;

      .el-date-editor {
        margin-right: 10px;
      }
    }

    .pagination-container {
      margin-top: 10px;
    }

    .tables {
      h3 {
        padding: 10px;
        width: 100%;
        margin-bottom: 10px;
        font-size: 14px;
        font-weight: bold;
        color: #444;
        background-color: #f2f2f2;
      }

      .table-item {
        margin-bottom: 20px;
      }
    }

    .el-select {
      width: 180px;
      margin-right: 15px;

      /deep/ .el-input {
        width: 100%;
        margin-bottom: 10px;
      }
    }
  }

}
</style>