<template>
  <div class="batch-edit-pwd">
    <el-card class="common-table-container">
      <div class="common-header-box">
        <h3>批量改密码</h3>
      </div>
      <main>
        <el-form :model="pwdRuleForm" :rules="rules" ref="pwdRuleForm" label-width="120px" class="demo-ruleForm">
          <el-form-item label="登录密码：" prop="password">
            <el-input show-password v-model="pwdRuleForm.password" clearable></el-input>
          </el-form-item>
          <el-form-item label="支付密码：" prop="payPassword">
            <el-input show-password v-model="pwdRuleForm.payPassword" clearable></el-input>
          </el-form-item>
          <el-form-item label="用户手机号：" prop="mobiles">
            <el-input type="textarea" rows="15" v-model="pwdRuleForm.mobiles" clearable></el-input>
            <p class="tips">* 多个手机号之间以英文逗号分割 ，上限50个</p>
          </el-form-item>
          <el-form-item>
            <el-button type="primary" @click="submitForm('pwdRuleForm')">提交</el-button>
            <el-button @click="resetForm('pwdRuleForm')">重置</el-button>
          </el-form-item>
        </el-form>
      </main>
      <div class="common-table-footer">
        
      </div>
    </el-card>
  </div>
</template>

<script>
export default {
  name: "BatchEditPwd",
  data() {
    return {
      pwdRuleForm: {
        password: "",
        payPassword: "",
        mobiles: "",
      },
      rules: {
        password: [
          { required: true, message: '请输入登录密码', trigger: 'blur' },
          { min: 5, message: '长度大于5个字符', trigger: 'blur' }
        ],
        payPassword: [
          { required: true, message: '请输入支付密码', trigger: 'blur' },
          { min: 5, message: '长度大于5个字符', trigger: 'blur' }
        ]
      }
    };
  },
  created() {
  },
  methods: {
    // 页数改变
    sizeChange(target, val) {
      // console.log(data, e)
      if (target === "sysPersonal") {  //这个地方你用switch做判断会好一点点
        this.tableParams.pageNum = val;
        this.pagePersonal(1) //系统未领个包
      } else if (target === "userPersonal") {
        this.userPersonalParams.pageNum = val;
        this.pagePersonal(2) //用户未领个包
      } else if (target === "sysGroup") {
        this.sysGroupParams.pageNum = val;
        this.pageGroup(1) //系统未领群包
      } else {
        this.userGroupParams.pageNum = val;
        this.pageGroup(2) //用户未领群包
      }
    },
    currentChange(target, val) {
      // console.log(data, e)
      if (target === "sysPersonal") {
        this.sysPersonalParams.pageSize = val;
        this.pagePersonal(1) //系统未领个包
      } else if (target === "userPersonal") {
        this.userPersonalParams.pageSize = val;
        this.pagePersonal(2) //用户未领个包
      } else if (target === "sysGroup") {
        this.sysGroupParams.pageSize = val;
        this.pageGroup(1) //系统未领群包
      } else {
        this.userGroupParams.pageSize = val;
        this.pageGroup(2) //用户未领群包
      }
    },
    // 批量修改用户密码
    async batchUpdatePwd() {
      let res = await this.$Axios.post("user/batch-update-password", JSON.stringify(this.pwdRuleForm), { contentType: 'application/json' });
      // console.log(res);
      let { code } = res.data;
      if (code === "000000") {
        this.$message.success("操作成功！");
      }
    },
    // 表单提交
    submitForm(formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          this.batchUpdatePwd(); //批量修改用户密码
          this.$refs[formName].resetFields(); //重置表单
        }
      });
    },
    // 重置表单
    resetForm(formName) {
      this.$refs[formName].resetFields();
    }
  },
};
</script>

<style lang="less" scoped>
.batch-edit-pwd {
  main {
    width: 50%;

    .tips {
      font-size: 12px;
      color: rgb(235, 4, 4);
    }
  }
}
</style>