<template>
  <div class="userConsult">
    <el-card class="common-search-box">
      <div class="common-search-container">
        <div class="search-item">
          <SearchDatePicker
            title="创建日期"
            :value="params.dateRange"
            @searchDateChange="searchDateChange"
          ></SearchDatePicker>
        </div>
        <div class="search-item">
          <span class="title">咨询状态</span>
          <el-select v-model="params.adminStatus" placeholder="请选择" clearable>
            <el-option
              v-for="item in consultTypeList"
              :key="item.adminStatus"
              :label="item.statusName"
              :value="item.adminStatus"
            >
            </el-option>
          </el-select>
        </div>
        <div class="search-item">
          <span class="title">搜索用户</span>
          <el-input
            v-model="params.userKeyword"
            suffix-icon="el-icon-search"
            placeholder="请输入名称、手机号、ID搜索"
            @input="params.userKeyword = params.userKeyword.trim()"
          ></el-input>
        </div>
        <div class="search-item search-btn">
          <el-button @click="query('query')" type="primary">查询</el-button>
          <el-button @click="reset">重置</el-button>
        </div>
      </div>
    </el-card>
    <el-card class="common-table-container">
      <div class="common-header-box">
        <h3>咨询列表</h3>
      </div>
      <el-table
        ref="multipleTable"
        :data="tableData"
        @select="handleSelectionChange"
        @select-all="handleSelectionChange"
        border
        height="100%"
      >
        <el-table-column type="selection" width="55"></el-table-column>
        <el-table-column
          v-for="item in tableColumn"
          :key="item.label"
          :label="item.label"
          :prop="item.prop"
          :min-width="item.width || 160"
        ></el-table-column>
        <el-table-column label="操作" width="200px">
          <template slot-scope="scope">
            <el-button type="text" @click="lookUp(scope.row)">查看</el-button>
          </template>
        </el-table-column>
      </el-table>

      <div class="common-table-footer">
        <div>
          <el-checkbox v-model="checkAll" @change="checkAllChange"
            style="margin-right: 30px;">全选</el-checkbox
          >
          <el-button @click="exportList">导出所选</el-button>
          <el-button @click="exportAllList">导出全部</el-button>
        </div>
        <el-pagination
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
          :current-page="params.pageNum"
          :page-sizes="[10, 20, 30, 40]"
          :page-size="params.pageSize"
          layout="total, sizes, prev, pager, next, jumper"
          :total="total"
        >
        </el-pagination>
      </div>
    </el-card>
  </div>
</template>

<script>
import utils from "@/api/utils";
export default {
  name: "UserConsult",
  data() {
    return {
      params: {
        dateRange: "",
        userKeyword: "",
        adminStatus: "",
        pageNum: 1,
        pageSize: 10,
      },
      params2: {
        dateRange: "",
        userKeyword: "",
        adminStatus: "",
        pageNum: 1,
        pageSize: 10,
      },
      consultTypeList: [],
      tableData: [], 
      total: 0, //总条数
      tableColumn: [
        { prop: "nickName", label: "发起用户" },
        { prop: "title", label: "描述" },
        { prop: "statusText", label: "状态" },
        { prop: "createTime", label: "时间" }
      ],
      // 选中全部数据，默认不选中
      checkAll: false,
      // 选中数据
      selectData: [],
    };
  },
  created() {
    this.queryConsultType();
  },
  methods: {
    queryConsultType() {
      this.$Axios.get('/customer-service/user-consult/status-list')
      .then( res=> {
        if( res.data.data ) {
          this.consultTypeList = res.data.data;
          this.query();
        }
      }).catch( err=>console.log(err))
    },
    searchDateChange(val) {
      this.params.dateRange = val;
    },
    // 查询
    query(type) {
      if (type == "query") {
        this.params.pageNum = 1;
        this.params2 = JSON.parse(JSON.stringify(this.params));
      } else {
        this.params2.pageNum = this.params.pageNum;
        this.params2.pageSize = this.params.pageSize;
      }
      let params = JSON.parse(
        JSON.stringify(utils.objDeleteNull(this.params2))
      );
      params.dateRange = this.params2.dateRange
        ? this.params2.dateRange.join(" ~ ")
        : null;
      this.$Axios
        .post("/customer-service/user-consult/page", JSON.stringify(params), {
          contentType: "application/json",
        })
        .then((res) => {
          if (res.data.data) {
            this.tableData = res.data.data.records;
            this.tableData.forEach(item => {
              item.statusText = utils.arrayToObj(this.consultTypeList,'adminStatus','statusName')[item.adminStatus];
            });
            this.total = res.data.data.total;
          }
        })
        .catch((err) => {
          console.log(err);
        });
    },
    // 重置
    reset() {
      this.params.dateRange = "";
      this.params.userKeyword = "";
      this.params.adminStatus = "";
      this.query('query');
    },
    // 查看
    lookUp(row) {
      console.log("row",row);
      this.$router.push({name: "ConsultDetail" ,query: {id: row.id,adminStatus:row.adminStatus}})
    },
    // 导出所选
    exportList() {
      if (this.selectData.length > 0) {
        let ids = utils.arrayOpera(this.selectData, "id");
        let params = {
          ids,
          userKeyword: this.params2.userKeyword,
          dateRange: this.params2.dateRange
            ? this.params2.dateRange.join(" ~ ")
            : null,
        };
        this.$Axios
          .post("/customer-service/user-consult/export", JSON.stringify(params), {
            responseType: "blob",
            contentType: "application/json",
          })
          .then((data) => {
            utils.excelDownload(data, "导出用户咨询列表.xls");
          })
          .catch((err) => {
            console.log(err);
          });
      } else {
        this.$message.warning("请选择导出项");
        return;
      }
    },
    // 导出全部
    exportAllList() {
      let params = {
        userKeyword: this.params2.userKeyword,
        dateRange: this.params2.dateRange
          ? this.params2.dateRange.join(" ~ ")
          : null,
      };
      this.$Axios
        .post("/customer-service/user-consult/export", JSON.stringify(params), {
          responseType: "blob",
          contentType: "application/json",
        })
        .then((data) => {
          utils.excelDownload(data, "导出全部用户咨询列表.xls");
        })
        .catch((err) => {
          console.log(err);
        });
    },
    handleCurrentChange(page) {
      this.params.pageNum = page;
      this.query();
    },
    handleSizeChange(val) {
      this.params.pageSize = val;
      this.query();
    },
    handleSelectionChange(val) {
      this.selectData = val;
      this.checkAll = val.length == this.tableData.length;
    },
    // 选中全部数据
    checkAllChange(val) {
      if (val) {
        this.$refs.multipleTable.toggleAllSelection()
      } else {
        this.$refs.multipleTable.clearSelection();
        this.selectData = [];
      }
    },
  },
};
</script>

